import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from './../ViewList2/ViewList'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columns = [
  { name: 'Claim Number', sortField: 'latest.claim_number' },
  { name: 'SOA No.', sortField: 'latest.soa.number' },
  { name: 'Bundle', sortField: 'latest.bundle_number' },
  { name: 'Due Date', sortField: 'latest.date.due' },
  { name: 'Facility Name', sortField: 'latest.facility.name' }
]

const ForReconciliationClaimsTile = props => {
  const { services, sizes } = props
  const [claims, setClaims] = React.useState({
    for_recon: { count: 0, data: [] },
    ticket: { count: 0, data: [] }
  })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'latest.date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    Promise.all([
      services.getClaims({
        category: 'for-recon',
        limit: 10,
        sort: 'latest.date.due|desc',
        range: 'this-week'
      }),
      services.getClaimCount({ category: 'for-recon' })
    ]).then(values => {
      const [claimResponse, claimCountResponse] = values
      const allClaims = claimResponse.data.data.results
      const allClaimsCount = claimCountResponse.data.data
      setClaims(prevState => ({ ...prevState, for_recon: { data: allClaims, count: allClaimsCount } }))
    })
  }, [])

  const handleFilterChange = value => {
    const category = 'for_recon'

    services.getClaims({ category, range: value, limit: 10, sort: 'latest.due.date|desc' }).then(({ data: res }) => {
      const { data } = res
      setClaims(prevState => ({ ...prevState, [category]: { ...prevState[category], data: data.results } }))
    })
  }

  const handleOnShowViewList = () => {
    let title = `Claims for Reconciliation`
    const category = 'for_recon'

    setViewList({ ...initialValue, show: true, title, category })
    services.getClaims({ category, limit: 10, sort: 'latest.date.due|desc' }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    services.getClaims(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  return (
    <React.Fragment>
      <DashboardTile
        count={{ color: 3, value: claims.for_recon.count }}
        sizes={sizes}
        title='Claims for Reconciliation'
        viewList={
          <Typography variant='caption' className='bold text-link' onClick={handleOnShowViewList}>
            View List
          </Typography>
        }
        onFilterChange={handleFilterChange}
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              {columns.map((item, index) => (
                <TableCell
                  key={index}
                  // id={item.id}
                >
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {claims.for_recon.data.map((item, index) => (
              <TableRow key={item.claim_number}>
                <TableCell>{item.claim_number}</TableCell>
                <TableCell>{item.soa.number}</TableCell>
                <TableCell>{item.bundle_number}</TableCell>
                <TableCell>{DateFormatter(item.date.due)}</TableCell>
                <TableCell>{item.facility.name}</TableCell>
                <TableCell id={`view-review-${index}`}>
                  <Link to={`/claims/recon/${item.claim_number}`}>
                    <Typography variant='caption' className='text-link underline' id={`update-bundle-${index}`}>
                      View Claim
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        columns={columns}
        searchTextPlaceHolder='Search for claim number, date or facility name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={item.claim_number}>
              <TableCell>{item.claim_number}</TableCell>
              <TableCell>{item.soa.number}</TableCell>
              <TableCell>{item.bundle_number}</TableCell>
              <TableCell>{DateFormatter(item.date.due)}</TableCell>
              <TableCell>{item.facility.name}</TableCell>
              <TableCell id={`view-review-${index}`}>
                <Link to={`/claims/recon/${item.claim_number}`}>
                  <Typography variant='caption' className='text-link underline' id={`update-bundle-${index}`}>
                    View Claim
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

ForReconciliationClaimsTile.displayName = 'DashboardTileComponent'

export default ForReconciliationClaimsTile
