import React from 'react'
import Paper from '@material-ui/core/Paper'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import { Link } from 'react-router-dom'
import Button from '@material-ui/core/Button'

// Components
import Table from 'SharedComponents/Table/Table'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import PaymentRequestServices from 'Services/Api/PaymentRequestServices'

// CSS
import classes from './SubmittedPaymentRequestList.module.scss'

// UTIL
import { DateFormatter } from 'Utilities/TextFormatter'

const headCells = [
  { id: 'bundle-number', name: 'Bundle/SOA No.', sortField: 'bundle_number' },
  { id: 'date-received', name: 'Date Recieved', sortField: 'date.received' },
  { id: 'due-date', name: 'Due Date', sortField: 'date.due' },
  { id: 'facility-name', name: 'Medical Partner Name (Facility)', sortField: 'facility.name' }
]

export default function PaymentRequestPage (props) {
  const processor_id = props.match.params.pr_officer

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'date.due',
    order: true,
    sort: 'date.due|desc'
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    PageTitleService('Submitted PR')
    BreadcrumbsService.setSelectedItems(['dashboard', 'submittedPaymentRequest'])

    PaymentRequestServices.getPaymentRequestBundles({ processor_id }, initialValue).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }, [])

  const handleOnSort = orderBy => e => {
    setViewList(p => ({ ...p, orderBy, order: !viewList.order }))
    handleOnViewListChange({ sort: `${orderBy}|${viewList.order ? 'asc' : 'desc'}` })
  }

  const handleOnViewListChange = params => {
    const _params = {
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    PaymentRequestServices.getPaymentRequestBundles({ processor_id }, _params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const [debounce, setDebounce] = React.useState(null)

  const handleSearchChange = e => {
    const search = e.target.value
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        setViewList(p => ({ ...p, search }))
        handleOnViewListChange({ search, page: 1 })
      }, 500)
    )
  }
  return (
    <div className={classes.root}>
      {viewList.data ? (
        <React.Fragment>
          <div className={classes.search}>
            <SearchTextComponent
              id='search'
              onChange={handleSearchChange}
              placeholder='Search for Bundle Number, Date or Facility Name'
            />
          </div>
          <div className={classes.table}>
            <Table
              TableContainerProps={{ style: { height: '100%', overflow: 'auto', position: 'relative' } }}
              TableProps={{ stickyHeader: true }}
              head={
                <TableRow>
                  {headCells.map(headCell => (
                    <TableCell key={headCell.sortField} id={headCell.id}>
                      <TableSortLabel
                        active={viewList.orderBy === headCell.sortField}
                        direction={viewList.order ? 'asc' : 'desc'}
                        onClick={handleOnSort(headCell.sortField)}
                      >
                        {headCell.name}
                      </TableSortLabel>
                    </TableCell>
                  ))}
                  <TableCell align='center' />
                </TableRow>
              }
              body={
                <React.Fragment>
                  {viewList.data.map((row, index) => (
                    <TableRow key={row.claim_number}>
                      <TableCell id={`bundle-number-${index}`}>{row.bundle_number}</TableCell>
                      <TableCell id={`date-received-${index}`}>{DateFormatter(row.date.received)}</TableCell>
                      <TableCell id={`due-date-${index}`}>{DateFormatter(row.date.due)}</TableCell>
                      <TableCell id={`facility-name-${index}`}>{row.facility.name}</TableCell>
                      <TableCell>
                        <Link to={`/payment-request/${row.bundle_number}/submitted`}>
                          <Typography variant='caption' className={`text-link underline`}>
                            View
                          </Typography>
                        </Link>
                      </TableCell>
                    </TableRow>
                  ))}
                </React.Fragment>
              }
              count={viewList.count}
              page={viewList.page}
              limit={viewList.limit}
              rowsPerPageOptions={[10, 15, 20]}
              onChange={handleOnViewListChange}
            />
          </div>
          <div align='right' className={classes.buttonWrapper}>
            <Button
              variant='contained'
              color='primary'
              className='button-negative'
              id='cancel'
              onClick={() => props.history.push(`/`)}
            >
              Close
            </Button>
          </div>
        </React.Fragment>
      ) : (
        ''
      )}
    </div>
  )
}
