import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import { Link } from 'react-router-dom'

// Components
import Table from 'SharedComponents/Table/Table'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'

// Styles
import classes from './ViewList.module.scss'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const headCells = [
  { id: 'claim-number', name: 'Claim Number', sortField: 'claim.claim_number' },
  { id: 'due-date', name: 'Due Date', sortField: 'claim.date.due' },
  { id: 'facility-name', name: 'Medical Partner Name (Facility)', sortField: 'claim.facility.name' }
]

const submittedHeadCells = [
  { id: 'approved-by', name: 'PR Processor', sortField: 'approved_by' },
  { id: 'total-bundles', name: 'Total Bundle/SOA', sortField: 'total_bundles' },
  { id: 'total-batches', name: 'Total Batches', sortField: 'total_batches' }
]
export default function CustomizedDialogs ({ viewList, setViewList, onViewListChange }) {
  const { category } = viewList
  const searchMessage =
    category === 'payment-request' ? 'Search for claim number, date or facility name' : 'Search for PR Processor'
  const handleOnClose = () => {
    setViewList(p => ({ ...p, show: false }))
  }

  const [debounce, setDebounce] = React.useState(null)

  const handleSearchChange = e => {
    const search = e.target.value
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        setViewList(p => ({ ...p, search }))
        onViewListChange({ search, page: 1 })
      }, 500)
    )
  }

  const handleOnSort = orderBy => e => {
    setViewList(p => ({ ...p, orderBy, order: !viewList.order }))
    onViewListChange({ sort: `${orderBy}|${viewList.order ? 'asc' : 'desc'}` })
  }
  return (
    <Dialog
      fullWidth
      maxWidth='lg'
      onClose={handleOnClose}
      aria-labelledby='customized-dialog-title'
      open={viewList.show}
      className={classes.root}
      PaperProps={{ style: { height: '100%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <Typography variant='h6' className={classes.title}>
            {viewList.title}
          </Typography>
          <IconButton aria-label='close' className={classes.button} onClick={handleOnClose}>
            <CloseIcon />
          </IconButton>
        </div>
      </DialogTitle>

      <DialogContent dividers className={classes.content}>
        <div className={classes.search}>
          <SearchTextComponent id='search' onChange={handleSearchChange} placeholder={searchMessage} />
        </div>
        <Table
          TableContainerProps={{ style: { height: '100%', overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          head={
            <TableRow>
              {category === 'payment-request' &&
                headCells.map(headCell => (
                  <TableCell key={headCell.sortField} id={headCell.id}>
                    <TableSortLabel
                      active={viewList.orderBy === headCell.sortField}
                      direction={viewList.order ? 'asc' : 'desc'}
                      onClick={handleOnSort(headCell.sortField)}
                    >
                      {headCell.name}
                    </TableSortLabel>
                  </TableCell>
                ))}
              {category === 'submitted' &&
                submittedHeadCells.map(headCell => (
                  <TableCell key={headCell.sortField} id={headCell.id}>
                    <TableSortLabel
                      active={viewList.orderBy === headCell.sortField}
                      direction={viewList.order ? 'asc' : 'desc'}
                      onClick={handleOnSort(headCell.sortField)}
                    >
                      {headCell.name}
                    </TableSortLabel>
                  </TableCell>
                ))}

              <TableCell align='center' />
            </TableRow>
          }
          body={
            <React.Fragment>
              {category === 'payment-request' &&
                viewList.data.map((row, index) => (
                  <TableRow key={row.claim_number}>
                    <TableCell id={`claim-number-${index}`}>{row.claim_number}</TableCell>
                    <TableCell id={`due-date-${index}`}>{DateFormatter(row.date.due)}</TableCell>
                    <TableCell id={`facility-name-${index}`}>{row.facility.name}</TableCell>
                    <TableCell>
                      <Link to={`/payment-request/${row.bundle_number}`}>
                        <Typography variant='caption' className={`text-link underline`}>
                          {category === 'payment-request' && 'Request Payment'}
                        </Typography>
                      </Link>
                    </TableCell>
                  </TableRow>
                ))}
              {category === 'submitted' &&
                viewList.data.map((row, index) => (
                  <TableRow key={row.claim_number}>
                    <TableCell id={`approved-by-${index}`}>{row.verifierName}</TableCell>
                    <TableCell id={`total-bundles-${index}`}>{row.total_bundles}</TableCell>
                    <TableCell id={`total-batches-${index}`}>{row.total_batches}</TableCell>
                    <TableCell>
                      <Link to={`/payment-requests/${row.id}/list`}>
                        <Typography variant='caption' className={`text-link underline`}>
                          {category === 'submitted' && 'View'}
                        </Typography>
                      </Link>
                    </TableCell>
                  </TableRow>
                ))}
            </React.Fragment>
          }
          count={viewList.count}
          page={viewList.page}
          limit={viewList.limit}
          rowsPerPageOptions={[10, 15, 20]}
          onChange={onViewListChange}
        />
      </DialogContent>
    </Dialog>
  )
}
