import React from 'react'
import Typography from '@material-ui/core/Typography'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import startCase from 'lodash/startCase'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList/ViewList'
// import ViewListEncoder from '../ViewListEncoder/ViewListEncoder'
import UserManagementServices from 'Services/Api/UserManagementServices'

const ClaimsEncoderTile = props => {
  const { services, sizes = { sm: 12 } } = props

  const [batches, setBatches] = React.useState({
    count: 0,
    data: []
  })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    getEncoders('CLAIMS_DETAILS_ENCODER')
  }, [])

  const getEncoders = role => {
    services
      .getEncodersBatches(role)
      .then(res => {
        const { data, count } = res.data.data
        setBatches(state => ({ ...state, count, data }))
      })
      .catch(err => {
        console.error(err)
        setBatches(state => ({ ...state, count: 0, data: [] }))
      })
  }

  const handleOnShowViewList = category => () => {
    setViewList({ ...initialValue, show: true, title: startCase(`${category} Batches`), category })
    services.getEncodersBatches('CLAIMS_DETAILS_ENCODER').then(({ data: res }) => {
      setViewList(state => ({ ...state, data: res.data.data, count: res.data.data.length }))
    })
  }

  // const handleOnViewListChange = params => {
  //   const _params = {
  //     category: viewList.category,
  //     limit: viewList.limit,
  //     page: viewList.page,
  //     search: viewList.search,
  //     sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
  //     ...params
  //   }

  //   services.getBatches(_params).then(({ data: res }) => {
  //     const { results, count, limit, page } = res.data
  //     setViewList(state => ({ ...state, data: results, count, limit, page }))
  //   })
  // }

  // const handleOnClickItemViewList = ({ category, batch_number }) => {
  //   if (category === 'open') handleOnEncode(batch_number)
  //   else if (category === 'my' || category === 'completed') props.history.push(`/batches/${batch_number}`)
  // }

  // const handleOnEncode = batch_number => {
  //   services.assignBatch({ batch_number }).then(({ data: res }) => {
  //     props.history.push(`/batches/${batch_number}`)
  //   })
  // }

  const handleViewClick = id => {
    return props.history.push(`/batches/assigned/${id}`)
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='claims-encoder'
        count={{ color: 3, value: batches.count, id: 'claims-encoder-count' }}
        isFilterShown={false}
        title='Claims Encoder'
        sizes={sizes}
        viewList={
          <Typography
            id='ob-view-list'
            variant='caption'
            className='bold text-link'
            onClick={handleOnShowViewList('claim-encoder')}
          >
            View List
          </Typography>
        }
        onFilterChange={() => {}}
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              <TableCell id='ces-encoder-name'>Encoder Name</TableCell>
              <TableCell align='center' id='ces-total-batches'>
                Total Batches
              </TableCell>
              <TableCell align='center' id='ces-total-rcs'>
                Total No. of RCS
              </TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {batches.data.map((item, index) => (
              <TableRow key={index}>
                <TableCell id={`ces-encoder-name-${index}`}>{item.username}</TableCell>
                <TableCell align='center' id={`ces-total-batches-${index}`}>
                  {item.total_batches}
                </TableCell>
                <TableCell align='center' id={`ces-total-rcs-${index}`}>
                  {item.total_rcs}
                </TableCell>
                <TableCell>
                  <Typography
                    id={`view-encoder-${index}`}
                    variant='caption'
                    className={`text-link underline`}
                    onClick={() => {
                      props.history.push(`/batches/assigned/${item.id}`)
                    }}
                  >
                    View
                  </Typography>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        handleViewClick={handleViewClick}
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={() => {}}
        onItemClick={() => {}}
      />
    </React.Fragment>
  )
}

ClaimsEncoderTile.displayName = 'DashboardTileComponent'

export default ClaimsEncoderTile
