import React from 'react'
import Paper from '@material-ui/core/Paper'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Button from '@material-ui/core/Button'
import Checkbox from '@material-ui/core/Checkbox'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'

// Shared Components
import Table from 'SharedComponents/Table/Table'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import TextField from 'SharedComponents/TextField/CustomTextField'

// Components
import AssignDialog from './AssignDialog/AssignDialog'
// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import BatchEncodingSupervisoryServices from 'Services/Api/BatchEncodingSupervisoryServices'
import UserManagementServices from 'Services/Api/UserManagementServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Styles
import classes from './CreatedBundlesPage.module.scss'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columnss = [
  { id: 'bundle-number', name: 'Bundle/SOA No.', sortField: 'bundle_number' },
  { id: 'date-received', name: 'Date Received', sortField: 'date.received' },
  { id: 'date-due', name: 'Due Date', sortField: 'date.due' },
  { id: 'facility-name', name: 'Medical Partner Name (Facility)', sortField: 'facility.name' }
]

export default function CreatedBundlesPage (props) {
  const [bundle, setBundle] = React.useState({
    data: [],
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    orderBy: 'date.due',
    order: true
  })

  const [batchEncoderId, setBatchEncoderId] = React.useState()
  const [openModal, setOpenModal] = React.useState()
  const [activeAssigning, setActiveAssigning] = React.useState()
  const [headerCheckBox, setHeaderCheckBox] = React.useState({})
  const [debounce, setDebounce] = React.useState(null)

  React.useEffect(() => {
    const _batchEncoderId = props.match.params.user_id
    setBatchEncoderId(_batchEncoderId)

    PageTitleService('Created Bundles')
    BreadcrumbsService.setSelectedItems(['dashboard', `createdBundles:${_batchEncoderId}`])

    BatchEncodingSupervisoryServices.getBundles({
      user_id: _batchEncoderId,
      params: { limit: 10, sort: 'date.due|asc' }
    }).then(({ data: response }) => {
      const { results: data } = response.data
      setBundle(state => ({ ...state, ...response.data, data }))
    })
  }, [])

  const handleSearchChange = e => {
    const search = e.target.value
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        setBundle(p => ({ ...p, search }))
        handleOnViewListChange({ search, page: 1 })
      }, 500)
    )
  }

  const handleOnSort = orderBy => e => {
    setBundle(p => ({ ...p, orderBy, order: !bundle.order }))
    handleOnViewListChange({ sort: `${orderBy}|${bundle.order ? 'asc' : 'desc'}` })
  }

  const handleOnViewListChange = params => {
    const _params = {
      limit: bundle.limit,
      page: bundle.page,
      search: bundle.search,
      sort: `${bundle.orderBy}|${bundle.order ? 'desc' : 'asc'}`,
      ...params
    }

    BatchEncodingSupervisoryServices.getBundles({
      user_id: batchEncoderId,
      params: _params
    }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setBundle(state => ({ ...state, data: results, count, limit, page }))

      setHeaderCheckBox({})
      LoadingService.loading(false)
    })
  }

  const handleOnItemSelect = index => {
    setBundle(state => {
      const data = state.data
      data[index].selected = !data[index].selected

      const selectedCount = data.filter(i => i.selected).length
      setHeaderCheckBox({ checked: !!selectedCount, indeterminate: selectedCount && selectedCount !== data.length })

      return { ...state, data }
    })
  }

  const handleOnSelectAllItem = () => {
    const { checked, indeterminate } = headerCheckBox

    setBundle(state => {
      return {
        ...state,
        data: state.data.map(i => ({
          ...i,
          selected: checked && indeterminate ? true : !(checked && !indeterminate)
        }))
      }
    })

    setHeaderCheckBox({ checked: (checked && indeterminate) || !checked })
  }

  const handleOnSubmit = ({ note, assignee }) => {
    LoadingService.loading(true)

    const bundle_numbers = []
    bundle.data.filter(i => i.selected && bundle_numbers.push(i.bundle_number))

    BatchEncodingSupervisoryServices.reassignBundles({
      user_id: batchEncoderId,
      data: {
        note,
        assignee,
        bundle_numbers
      }
    }).then(res => {
      handleOnViewListChange({})
    })
  }

  return (
    <div className={classes.root}>
      <Paper className={classes.content}>
        <div className={classes['first-container']}>
          <div className={classes.action}>
            <Button
              id='cancelreassign-btn'
              variant='contained'
              color='primary'
              className='button-neutral'
              onClick={() => setActiveAssigning(true)}
              disabled={!!activeAssigning}
            >
              Re-Assign
            </Button>
          </div>

          <div className={classes.search}>
            <SearchTextComponent
              id='search'
              onChange={handleSearchChange}
              placeholder='Search for bundle number, date or facility name'
            />
          </div>
        </div>

        <div className={classes.table}>
          <Table
            TableContainerProps={{ style: { flex: 1, overflow: 'auto', position: 'relative' } }}
            TableProps={{ stickyHeader: true }}
            head={
              <TableRow>
                {activeAssigning && (
                  <TableCell id='checkbox-selecte-all' padding='checkbox' size='small'>
                    <Checkbox
                      color='primary'
                      style={{ marginTop: '30px' }}
                      disabled={!bundle.data.length}
                      checked={headerCheckBox.checked || false}
                      indeterminate={headerCheckBox.indeterminate || false}
                      onChange={handleOnSelectAllItem}
                    />
                  </TableCell>
                )}

                {columnss.map(column => (
                  <TableCell key={column.sortField} id={column.id}>
                    <TableSortLabel
                      active={bundle.orderBy === column.sortField}
                      direction={bundle.order ? 'asc' : 'desc'}
                      onClick={handleOnSort(column.sortField)}
                    >
                      {column.name}
                    </TableSortLabel>

                    <TextField
                      id={`text-field-${column.id}`}
                      // value={newBundle.bundle_number}
                      TextFieldStyle={{ margin: 0 }}
                      fullWidth
                    />
                  </TableCell>
                ))}

                <TableCell align='center' />
              </TableRow>
            }
            body={bundle.data.map((item, index) => (
              <TableRow key={item.bundle_number}>
                {activeAssigning && (
                  <TableCell id='checkbox-selecte-all' padding='checkbox' size='small'>
                    <Checkbox
                      color='primary'
                      onChange={() => handleOnItemSelect(index)}
                      checked={item.selected || false}
                    />
                  </TableCell>
                )}

                <TableCell id={`bundle-number-${index}`}>{item.bundle_number}</TableCell>
                <TableCell id={`received-date-${index}`}>{DateFormatter(item.date.received)}</TableCell>
                <TableCell id={`due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
                <TableCell id={`facility-name-${index}`}>{item.facility.name}</TableCell>
                <TableCell id={`view-work-${index}`}>
                  <Link to={`/batch-encoder-work/${batchEncoderId}/bundles/${item.bundle_number}`}>
                    <Typography variant='caption' className='text-link underline' id={`view-bundle-${index}`}>
                      View
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
            count={bundle.count}
            page={bundle.page}
            limit={bundle.limit}
            rowsPerPageOptions={[10, 15, 20]}
            onChange={handleOnViewListChange}
          />
        </div>

        <div className={classes.footer}>
          {activeAssigning && (
            <Typography variant='subtitle2' className={classes.title}>
              Total Bundle Selected: {bundle.data.filter(i => i.selected).length}
            </Typography>
          )}

          <div className={classes.actions}>
            {activeAssigning ? (
              <React.Fragment>
                <Button
                  variant='contained'
                  color='primary'
                  className='button-negative'
                  onClick={() => {
                    setActiveAssigning()
                  }}
                  id='cancel'
                >
                  Cancel
                </Button>

                <Button
                  variant='contained'
                  color='primary'
                  className={`${classes.save} button-positive`}
                  onClick={() => setOpenModal(true)}
                  disabled={!(headerCheckBox.checked || false)}
                  id='save'
                >
                  Assign
                </Button>
              </React.Fragment>
            ) : (
              <Button
                variant='contained'
                color='primary'
                className='button-negative'
                onClick={() => props.history.push('/')}
                id='cancel'
              >
                Close
              </Button>
            )}
          </div>
        </div>
      </Paper>
      <AssignDialog
        show={openModal}
        setShow={setOpenModal}
        services={UserManagementServices}
        onSubmit={handleOnSubmit}
      />
    </div>
  )
}
