import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import Checkbox from '@material-ui/core/Checkbox'

// Components
import EnhancedTable from 'SharedComponents/EnhancedTable/EnhancedTable'
import TableFooterComponent from 'SharedComponents/TableFooterComponent/TableFooterComponent'
import Alert from 'SharedComponents/Alert/Alert'

// Style
import classes from './BatchList.module.scss'

const headCells = [
  { id: 'batch-no', numeric: false, disablePadding: false, label: 'Batch No' },
  { id: 'batch-claim-type', numeric: false, disablePadding: false, label: 'Claim Type' },
  { id: 'batch-total-rcs', numeric: false, disablePadding: false, label: 'Total No. of RCS' },
  {}
]

export default function BatchList (props) {
  const {
    batches,
    onDelete,
    openDialog,
    onCancel,
    canEdit,
    openError,
    onEdit,
    setForDeletion,
    forDeletion,
    isBundleCompleted = false
  } = props
  const [batch, setBatch] = React.useState([])

  const getMessage = batches => {
    let deleteMessage = 'Are you sure you want to delete this batch?'
    if (batches.batch_numbers.length > 1) {
      deleteMessage = 'Are you sure you want to delete these batches?'
    }
    return deleteMessage
  }

  React.useEffect(() => {
    const data = batches.map(i => ({
      ...i,
      selected: false
    }))
    setBatch({
      data: data,
      loading: false,
      indeterminate: undefined,
      checked: undefined
    })
    setForDeletion({ batch_numbers: [] }) // Resets the batch list.
  }, [batches])

  const handleClick = ({ batch, index }) => {
    const _batches = {
      ...batch,
      data: batch.data.map((i, _index) => {
        if (_index === index) i.selected = !i.selected
        return i
      })
    }
    setBatch(_batches)
    changeSelectAllState({ data: _batches })
  }

  const handleSelectAllClick = event => {
    let _batches = batch

    if (_batches.data) {
      _batches.data.map(i => {
        i.selected = _batches.indeterminate ? true : !_batches.checked
        return i
      })
    }

    setBatch(_batches)
    changeSelectAllState({ data: _batches })
  }

  const changeSelectAllState = ({ data }) => {
    let _batches = data || batches
    let selected_count = 0
    let batchNumbers = []
    if (_batches.data) {
      for (let b of _batches.data) {
        if (b.selected) {
          selected_count++
          batchNumbers.push(b.batch_number)
        }
      }
    }

    let indeterminate = selected_count ? selected_count < _batches.data.length : false
    let checked = selected_count ? selected_count === _batches.data.length : false
    _batches = { ..._batches, indeterminate, checked }
    setBatch(_batches)
    setForDeletion({ batch_numbers: batchNumbers })
  }

  return (
    <React.Fragment>
      <EnhancedTable
        headCells={headCells}
        // handleRequestSort={handleRequestSort}
        handleSelectAllClick={handleSelectAllClick}
        checkAll={canEdit}
        indeterminate={!!batch.indeterminate}
        checked={!!batch.checked}
        // allowCheck
        // allowCheckDisabled={Boolean(isBundleCompleted)}
      >
        <TableBody>
          {batch.data &&
            batch.data.map((i, index) => (
              <TableRow key={i.batch_number}>
                {canEdit && (
                  <TableCell id={`checkbox-${i}`} padding='checkbox'>
                    <Checkbox
                      color='default'
                      disabled={Boolean(isBundleCompleted)}
                      checked={i.selected}
                      onClick={event => handleClick({ index, batch })}
                    />
                  </TableCell>
                )}
                <TableCell id={`batch-no-${index}`}>{i.batch_number}</TableCell>
                <TableCell id={`batch-claim-type-${i.claim.type}`}>{i.claim.type}</TableCell>
                <TableCell id={`batch-total-rcs-${i.total_rcs}`}>{i.total_rcs}</TableCell>
                {canEdit && (
                  <TableCell className={classes.buttonWrapper} align='right'>
                    <Button variant='contained' color='primary' className='button-neutral' onClick={() => onEdit(i)}>
                      Edit
                    </Button>
                  </TableCell>
                )}
              </TableRow>
            ))}
        </TableBody>
      </EnhancedTable>
      <Alert
        open={openDialog}
        message={getMessage(forDeletion)}
        cancelLabel='No'
        okLabel='Yes'
        onCancel={onCancel}
        onOk={onDelete}
      />
      <Alert
        open={openError}
        message='Delete Error: This batch has existing claim records'
        cancelLabel='Ok'
        onCancel={onCancel}
      />
    </React.Fragment>
  )
}
