import instance from './instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'

const axios = instance('/client')
export default class CustomerCareService {
  static getLoaDetails ({ approval_code }) {
    return axios({
      url: `api/customer-care/member-utilization-loa/approval/${approval_code}`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['404']]: 1 }
    })
  }

  static getUtilizationLoa ({ params }) {
    return axios({
      url: `api/customer-care/member-utilization-loa`,
      method: 'GET',
      params: {
        filter: params
      }
    })
  }

  static getContractList ({ member_account_number }) {
    return axios({
      url: `api/customer-care/members/${member_account_number}`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['404']]: 1 }
    })
  }

  // Direct API call Customer Care
  static getBenefitBasePlan ({ params }) {
    return axios({
      url: `api/base-benefit-plans`,
      method: 'GET'
    })
  }

  static getBenefitPlanTree ({ contract_oid }) {
    return axios({
      url: `api/benefit-plans/tree/${contract_oid}`,
      method: 'GET'
    })
  }

  static getMemberContract ({ member_oid, contract_oid }) {
    return axios({
      url: `api/customer-care/members/${member_oid}/contracts/${contract_oid}`,
      method: 'GET'
    })
  }

  static getMemberContractBenefits ({ member_oid, contract_oid }) {
    return axios({
      url: `api/members/${member_oid}/contracts/${contract_oid}/benefits`,
      method: 'GET'
    })
  }

  static getMemberContractPDF ({ member_oid, contract_oid }) {
    return axios({
      url: `api/base-benefit-plans/${member_oid}/contracts`,
      method: 'GET',
      param: { id: contract_oid }
    })
  }

  static getMember ({ member_account_number }) {
    return axios({
      url: `api/customer-care/members/${member_account_number}`,
      method: 'GET'
    })
  }

  static getProUser (params) {
    return axios({
      url: `api/users/pro`,
      method: 'GET',
      params
    })
  }
}
