import React from 'react'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'

// Styles
import classes from './ButtonFooter.module.scss'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { resetReassign, setReassignNotes } from '../../../ClaimsDetailsEncoder/Store/actions'

// lodash
import { sortBy } from 'lodash'

// Component
import AssignDialog from '../AssignDialog/AssignDialog'

export default function ClaimFooter (props) {
  const {
    handleOnSubmitReassign = () => {},
    handleOnCancel = () => {},
    handleOnClose = () => {},
    selectStatus,
    UserManagementServices = () => {},
    count
  } = props

  const dispatch = useDispatch()
  // const [count, setCount] = React.useState(0)
  const [dropDownUsers, setDropDownUsers] = React.useState([])

  const reassign = useSelector(state => state.reassign)
  const [dialog, setDialog] = React.useState({
    assign: false,
    alert: false
  })

  const initDropdownUsers = async ({ roles }) => {
    UserManagementServices.getUsersByRole(roles).then(({ data }) => {
      let users = data.map(user => ({ text: `${user.first_name} ${user.last_name}`, value: user._id }))

      setDropDownUsers(sortBy(users, ['text']))
    })
  }

  // const handleOnsubmitReassign = (batches, user_id) => {
  //   console.log(batches)
  //   console.log(user_id)
  // }

  const handleOnOpenDialog = () => {
    if (count === 0) return
    dispatch(resetReassign())
    initDropdownUsers({ roles: 'CLAIMS_DETAILS_ENCODER' })
    // setReassignErrors({ message: '', assignee: '' })
    dispatch(setReassignNotes(''))
    // setReassignModal(state => ({ ...state, dialog: true }))
    setDialog(state => ({ ...state, assign: true }))
  }

  return (
    <div className={classes.root}>
      <div className={classes['counter']}>
        {selectStatus ? <Typography>Total Batches Selected: {count} </Typography> : ''}
      </div>
      <div className={classes['buttons']}>
        {!selectStatus ? (
          <React.Fragment>
            <Button
              variant='contained'
              color='primary'
              id='btn-close'
              className='button-negative'
              onClick={() => handleOnClose()}
            >
              Close
            </Button>
          </React.Fragment>
        ) : (
          <React.Fragment>
            <div>
              <Button
                variant='contained'
                color='primary'
                id='save-for-now'
                className='button-positive'
                onClick={() => handleOnOpenDialog()}
              >
                Assign
              </Button>
              <Button
                variant='contained'
                color='primary'
                id='save-for-now'
                className='button-negative'
                onClick={() => handleOnCancel()}
              >
                Cancel
              </Button>
            </div>
          </React.Fragment>
        )}
      </div>
      <AssignDialog
        handleOnSubmitReassign={handleOnSubmitReassign}
        dropDownUsers={dropDownUsers}
        dialog={dialog}
        setDialog={setDialog}
      />
    </div>
  )
}
