import React from 'react'
import TextField from 'SharedComponents/TextField/CustomTextField'
import Typography from '@material-ui/core/Typography'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Button from '@material-ui/core/Button'
import Checkbox from '@material-ui/core/Checkbox'
import Paper from '@material-ui/core/Paper'
import ButtonFooter from '../../Components/ButtonFooter/ButtonFooter'

import { DateFormatter } from 'Utilities/TextFormatter'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import ClaimsEncoderSupervisorServices from 'Services/Api/ClaimsEncoderSupervisorServices/ClaimsEncoderSupervisorServices'
import UserManagementServices from 'Services/Api/UserManagementServices'

// Store
import { useSelector } from 'react-redux'

// Components
import Table from 'SharedComponents/Table/Table'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'

// Styles
import classes from './CreatedBatchesPage.module.scss'

export default function CreatedBatchesPage (props) {
  const [selectStatus, setSelectStatus] = React.useState(false)

  const [headcell, setHeadCell] = React.useState([
    { id: 's-batch-number', name: ' Batch Number', sortField: 'batch_number', match: 'any' },
    { id: 's-date-received', name: ' Date Received', sortField: 'date_received', match: 'date' },
    { id: 's-due-date', name: ' Due Date', sortField: 'date_due', match: 'date' },
    { id: 's-total-rcs', name: ' Total No. of RCS', sortField: 'total_rcs', match: 'number' },
    { id: 's-facility-name', name: ' Medical Partner Name (Facility)', sortField: 'facility_name', match: 'any' }
  ])

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    // data: [],
    filter: {},
    orderBy: 'batch_number',
    order: true,
    rowsPerPageOptions: [10, 15, 20]
  }

  // const [viewList, setViewList] = React.useState(initialValue)

  const [batch, setBatch] = React.useState({})
  const [batches, setBatches] = React.useState(initialValue)
  const [id, setId] = React.useState(null)
  const [selected, setSelected] = React.useState({ batch_numbers: [] })
  const reassign = useSelector(state => state.reassign)
  const [debounce, setDebounce] = React.useState(null)

  React.useEffect(() => {
    PageTitleService('Created Batches')
    BreadcrumbsService.setSelectedItems(['dashboard', `assigned:${props.match.params._id}`])
    LoadingService.loading(true)

    const _id = props.match.params._id
    getBatches(batches)
    setId(_id)
  }, [props.match.params.id])

  const getBatches = params => {
    ClaimsEncoderSupervisorServices.getEncoderBatches({
      id: props.match.params._id,
      search: params.search,
      sort: `${params.orderBy}|${params.order ? 'asc' : 'desc'}`,
      page: params.page,
      filter: params.filter,
      limit: params.limit
    })
      .then(res => {
        const { results = [], count, limit, page } = res.data.data
        setBatches(state => ({ ...state, count, limit, page }))

        const data = results.map(batch => ({ ...batch, checked: false }))

        if (data) setSelectStatus(false)
        setBatch({
          data: data,
          loading: false,
          indeterminate: undefined,
          checked: undefined
        })
        LoadingService.loading(false)
      })
      .catch(err => {
        console.error(err)
      })
  }

  const handleSelectAll = event => {
    let _batches = batch

    if (_batches.data) {
      _batches.data.map(i => {
        i.checked = _batches.indeterminate ? true : !_batches.checked
        return i
      })
    }

    setBatch(_batches)
    changeSelectAllState({ data: _batches })
  }

  const handleItemSelect = ({ batch, index }) => {
    const _batches = {
      ...batch,
      data: batch.data.map((i, _index) => {
        if (_index === index) i.checked = !i.checked
        return i
      })
    }
    setBatch(_batches)
    changeSelectAllState({ data: _batches })
  }

  const changeSelectAllState = ({ data }) => {
    let _batches = data || batches
    let selected_count = 0
    let batchNumbers = []
    if (_batches.data) {
      for (let b of _batches.data) {
        if (b.checked) {
          selected_count++
          batchNumbers.push(b.batch_number)
        }
      }
    }

    let indeterminate = selected_count ? selected_count < _batches.data.length : false
    let checked = selected_count ? selected_count === _batches.data.length : false
    _batches = { ..._batches, indeterminate, checked }
    setBatch(_batches)

    setSelected({ batch_numbers: batchNumbers })
  }

  const handleOnCancel = () => {
    let _data = batch.data.map(i => ({ ...i, checked: false }))

    setBatch(state => ({ ...state, data: _data, checked: false, indeterminate: undefined }))
    setSelectStatus(false)
    setSelected({ batch_numbers: [] })
  }

  const handleOnSubmitReassign = () => {
    LoadingService.loading(true)
    ClaimsEncoderSupervisorServices.reassign({
      selected: selected.batch_numbers,
      id: reassign.assignee.id,
      notes: reassign.note
    }).then(() => {
      setSelected({ batch_numbers: [] })
      getBatches(batches)
      LoadingService.loading(false)
    })
  }

  const handleOnSearchAllChange = args => {
    getBatches({ ...batches, ...args })
  }

  const handleOnSearch = args => {
    const { field, value, match } = args
    const params = {
      ...batches,
      filter: { [field]: { value, match } }
    }
    getBatches(params)
  }

  const handleOnPaginate = args => {
    const params = {
      ...batches,
      ...args
    }
    setBatches(state => ({ ...state, ...args }))
    getBatches(params)
  }

  const handleTableRenderer = type => {
    if (type === 'headcell') {
      return (
        <TableRow>
          {selectStatus ? (
            <TableCell padding='checkbox'>
              <Checkbox
                color='default'
                indeterminate={!!batch.indeterminate}
                checked={!!batch.checked}
                onClick={event => handleSelectAll()}
              />
            </TableCell>
          ) : null}
          {headcell.map(headCell => (
            <TableCell key={headCell.sortField} id={headCell.id}>
              <TableSortLabel
                active={batches.orderBy === headCell.sortField}
                direction={batches.order ? 'asc' : 'desc'}
                onClick={event => handleOnPaginate({ orderBy: headCell.sortField, order: !batches.order })}
              >
                {headCell.name}
              </TableSortLabel>
              <TextField
                className={`${classes.search}`}
                onChange={event => {
                  const search = event.target.value
                  clearTimeout(debounce)
                  setDebounce(
                    setTimeout(() => {
                      handleOnSearch({
                        field: headCell.sortField,
                        value: search,
                        match: headCell.match
                      })
                    }, 500)
                  )
                }}
              />
            </TableCell>
          ))}

          <TableCell align='center' />
        </TableRow>
      )
    } else if (type === 'body') {
      return batch.data
        ? batch.data.map((row, index) => (
            <TableRow key={index}>
              {selectStatus ? (
                <TableCell padding='checkbox'>
                  <Checkbox
                    checked={row.checked}
                    color='default'
                    onClick={event => handleItemSelect({ index, batch })}
                  />
                </TableCell>
              ) : null}
              <TableCell id={`batch-number-${index}`}>{row.batch_number}</TableCell>
              <TableCell id={`date-received-${index}`}>{DateFormatter(row.date_received)}</TableCell>
              <TableCell id={`due-date-${index}`}>{DateFormatter(row.date_due)}</TableCell>
              <TableCell align='center' id={`total-rcs-${index}`}>
                {row.total_rcs}
              </TableCell>
              <TableCell id={`facility-name-${index}`}>{row.facility_name}</TableCell>
              <TableCell id={`view-encoder-${index}`}>
                <Typography
                  variant='caption'
                  className='text-link underline'
                  onClick={() => props.history.push(`/batches/assigned/${id}/${row.batch_number}`)}
                >
                  View
                </Typography>
              </TableCell>
            </TableRow>
          ))
        : null
    }
  }

  return (
    <div className={classes.root}>
      <Paper>
        <React.Fragment>
          <div className={classes['search-container']}>
            <div className={classes['reassign']}>
              <Button
                disabled={selectStatus}
                style={{ height: '48px', marginBottom: '17px' }}
                variant='contained'
                color='secondary'
                className='button-positive'
                onClick={() => {
                  setSelectStatus(true)
                }}
              >
                Reassign
              </Button>
            </div>
            <div className={classes['search']}>
              <SearchTextComponent
                id='search'
                onChange={e => {
                  const search = e.target.value
                  clearTimeout(debounce)
                  setDebounce(
                    setTimeout(() => {
                      handleOnSearchAllChange({ search, page: 1 })
                      setBatches(state => ({ ...state, search }))
                    }, 500)
                  )
                }}
                placeholder='Search for batch number, date or facility name'
              />
            </div>
          </div>
          <div className={classes['table-container']}>
            <Table
              TableContainerProps={{ style: { height: '100%', overflow: 'auto', position: 'relative' } }}
              TableProps={{ stickyHeader: true }}
              head={handleTableRenderer('headcell')}
              body={handleTableRenderer('body')}
              count={batches.count}
              page={batches.page}
              limit={batches.limit}
              pagination
              rowsPerPageOptions={batches.rowsPerPageOptions}
              onChange={args => handleOnPaginate(args)}
              cesFooter
            />
          </div>

          <ButtonFooter
            selectStatus={selectStatus}
            count={selected.batch_numbers.length ? selected.batch_numbers.length : 0}
            handleOnSubmitReassign={handleOnSubmitReassign}
            UserManagementServices={UserManagementServices}
            handleOnCancel={() => handleOnCancel()}
            handleOnClose={() => props.history.push(`/`)}
          />
        </React.Fragment>
      </Paper>
    </div>
  )
}
