import React from 'react'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'

// Styles
import classes from './ButtonFooter.module.scss'

// Component
import AssignDialog from '../AssignDialog/AssignDialog'

export default function ClaimFooter (props) {
  const {
    handleOnSubmit = () => {},
    handleOnCancel = () => {},
    handleOnClose = () => {},
    selectStatus,
    claimsCount = 0
  } = props

  const [dialog, setDialog] = React.useState({
    assign: false,
    alert: false
  })

  return (
    <div className={classes.root}>
      <div className={classes['counter']}>
        {selectStatus ? <Typography>Total Claims Selected: {claimsCount} </Typography> : ''}
      </div>
      <div className={classes['buttons']}>
        {!selectStatus ? (
          <React.Fragment>
            <Button
              variant='contained'
              color='primary'
              id='btn-close'
              className='button-negative'
              onClick={() => handleOnClose()}
            >
              Close
            </Button>
          </React.Fragment>
        ) : (
          <React.Fragment>
            <div>
              <Button
                variant='contained'
                color='primary'
                id='save-for-now'
                className='button-positive'
                onClick={() => setDialog(state => ({ ...state, assign: true }))}
              >
                Assign
              </Button>
              <Button
                variant='contained'
                color='primary'
                id='save-for-now'
                className='button-negative'
                onClick={() => handleOnCancel()}
              >
                Cancel
              </Button>
            </div>
          </React.Fragment>
        )}
      </div>
      <AssignDialog dialog={dialog} setDialog={setDialog} handleOnSubmit={handleOnSubmit} />
    </div>
  )
}
