import React from 'react'
import Typography from '@material-ui/core/Typography'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../VerifierList/VerifierList'

// Services
import UserManagementServices from 'Services/Api/UserManagementServices'

const ClaimsForApprovalTile = props => {
  const { services } = props
  const [subordinates, setSubordinates] = React.useState([])

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'latest.date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)
  const [claimsCount, setClaimsCount] = React.useState(0)

  React.useEffect(() => {
    Promise.all([services.getClaimsBySubbordinateCount(), UserManagementServices.getClaimsAnalyst()]).then(res => {
      const [count, users] = res

      setClaimsCount(count.data.data)

      const fetchDetails = async () => {
        return Promise.all(
          users.data.map(async user => {
            const userObject = {}
            userObject.verifierName = `${user.first_name} ${user.last_name}`
            userObject.totalBatches = 0
            userObject.totalClaims = 0
            userObject.id = user._id
            // userObject.totalClaims = await services
            //   .getClaimsBySubbordinateCount({ user_id: user._id })
            //   .then(count => {
            //     return count.data.data
            //   })
            //   .catch(err => {
            //     console.error(`Error fetching claims for user ${userObject.verifierName}: `, err)
            //     return 0
            //   })
            return userObject
          })
        )
      }

      fetchDetails()
        .then(res => setSubordinates(prevState => [...prevState, ...res]))
        .catch(err => console.error('Error: ', err))
    })
  }, [])

  const handleOnReviewUser = (params = null) => () => {
    if (!params) return false
    props.history.push(`/list/${params}`)
  }

  const handleOnShowViewList = category => () => {
    let title = 'Claims Verifier List'

    setViewList({ ...initialValue, show: true, title })

    setViewList(state => ({
      ...state,
      data: subordinates,
      count: subordinates.length,
      limit: subordinates.length,
      page: 1
    }))
  }

  const handleOnViewListChange = params => {
    const { search } = params
    let data = []
    subordinates.map(subordinate => {
      if (new RegExp(search, 'g').test(subordinate.verifierName)) {
        data.push(subordinate)
      }
    })
    setViewList(state => ({ ...state, data }))
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='claims-verifier'
        count={{ color: 3, value: claimsCount, id: 'claims-verifier-count' }}
        sizes={{ sm: 12 }}
        title='Claims Verifier'
        isFilterShown={false}
        viewList={
          <Typography id='cv-view-list' variant='caption' className='bold text-link' onClick={handleOnShowViewList()}>
            View List
          </Typography>
        }
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              <TableCell id='cv-verifier-name'>Verifier Name</TableCell>
              <TableCell id='cv-total-batches'>Total Batches</TableCell>
              <TableCell id='cv-total-claims'>Total Claims</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {subordinates &&
              subordinates.map((item, index) => (
                <TableRow key={`verifier-list-item-${index}`}>
                  <TableCell id={`cv-verifier-name-${index}`}>{item.verifierName}</TableCell>
                  <TableCell id={`cv-total-batches-${index}`}>{item.totalBatches}</TableCell>
                  <TableCell id={`cv-total-claims-${index}`}>{item.totalClaims}</TableCell>
                  <TableCell id={`cv-view-user-${index}`}>
                    <Typography
                      id={`view-user-${index}`}
                      variant='caption'
                      className={`text-link underline`}
                      onClick={handleOnReviewUser(item.id)}
                    >
                      View
                    </Typography>
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        onItemClick={handleOnReviewUser}
      />
    </React.Fragment>
  )
}

ClaimsForApprovalTile.displayName = 'DashboardTileComponent'

export default ClaimsForApprovalTile
