import React from 'react'
import Typography from '@material-ui/core/Typography'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList/ViewList'

// Utilities
import { DateFormatter, NameFormatter } from 'Utilities/TextFormatter'

const MedicalCollectiblesTile = props => {
  const { services } = props
  const [claims, setClaims] = React.useState({
    count: 0,
    data: []
  })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'latest.date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    Promise.all([
      services.getItems({
        limit: 10,
        sort: 'latest.date.due|desc',
        range: 'all'
      }),
      services.getItemCount()
    ]).then(values => {
      const [claimResponse, claimCountResponse] = values
      const allClaims = claimResponse.data.data.results
      const allClaimsCount = claimCountResponse.data.data
      setClaims(prevState => ({ ...prevState, data: allClaims, count: allClaimsCount }))
    })
  }, [])

  const handleOnReview = claim_number => {
    props.history.push(`/claims/review/${claim_number}`)
  }

  const handleOnShowViewList = category => () => {
    let title = 'Medical Collectibles'

    setViewList({ ...initialValue, show: true, title, category })
    services
      .getItems({
        limit: 10,
        sort: 'latest.date.due|desc',
        range: 'all'
      })
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    services.getItems(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnClickItemViewList = ({ category, claim_number }) => {
    handleOnReview(claim_number)
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='medical-collectibles'
        count={{ color: 3, value: claims.count, id: 'medical-collectibles-count' }}
        sizes={{ sm: 12 }}
        title='Medical Collectibles'
        isFilterShown={false}
        viewList={
          <Typography
            id='mc-view-list'
            variant='caption'
            className='bold text-link'
            onClick={handleOnShowViewList('escalated')}
          >
            View List
          </Typography>
        }
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              <TableCell id='mc-claim-no'>Claim No.</TableCell>
              <TableCell id='mc-account-no'>Account No.</TableCell>
              <TableCell id='mc-account-name'>Account Name</TableCell>
              <TableCell id='mc-member-name'>Member Name</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {claims.data.map((item, index) => (
              <TableRow key={item.claim_number}>
                <TableCell id={`mc-claim-no-${index}`}>{item.claim_number}</TableCell>
                <TableCell id={`mc-account-no-${index}`}>{DateFormatter(item.date.due)}</TableCell>
                <TableCell id={`mc-account-name-${index}`}>{''}</TableCell>

                <TableCell id={`mc-member-name-${index}`}>{NameFormatter(item.member).format1}</TableCell>
                <TableCell id={`mc-view-claim-${index}`}>
                  <Typography
                    id={`view-claim-${index}`}
                    variant='caption'
                    className={`text-link underline`}
                    onClick={() => handleOnReview(item.claim_number)}
                  >
                    View Claim
                  </Typography>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        onItemClick={handleOnClickItemViewList}
      />
    </React.Fragment>
  )
}

MedicalCollectiblesTile.displayName = 'DashboardTileComponent'

export default MedicalCollectiblesTile
