import Button from '@material-ui/core/Button'
import ButtonFooter from '../../Components/ButtonFooter/ButtonFooter'
import Checkbox from '@material-ui/core/Checkbox'
import { Link } from 'react-router-dom'
import React from 'react'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import TextField from 'SharedComponents/TextField/CustomTextField'
import Typography from '@material-ui/core/Typography'
import Paper from '@material-ui/core/Paper'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import ClaimsVerifierSupervisorServices from 'Services/Api/ClaimsVerifierSupervisorServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'

// Components
import Table from 'SharedComponents/Table/Table'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'

// Styles
import classes from './ClaimsList.module.scss'

export default function ClaimsPage (props) {
  const userId = props.match.params.user_id
  const [selectStatus, setSelectStatus] = React.useState(false)

  const headcell = [
    { id: 's-claim-number', name: ' Claim Number', sortField: 'claim_number', match: 'any' },
    { id: 's-date-received', name: ' Date Received', sortField: 'date_received', match: 'date' },
    { id: 's-due-date', name: ' Due Date', sortField: 'date_due', match: 'date' },
    { id: 's-facility-name', name: ' Medical Partner Name (Facility)', sortField: 'facility_name', match: 'any' }
  ]
  const [claims, setClaims] = React.useState({
    category: 'my',
    show: false,
    title: '',
    count: 0,
    search: '',
    filter: {},
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'claim_number',
    order: true,
    rowsPerPageOptions: [10, 15, 20]
  })
  const [claimsCount, setClaimsCount] = React.useState(0)

  React.useEffect(() => {
    PageTitleService('For Adjudication')
    BreadcrumbsService.setSelectedItems(['assigned'])
    LoadingService.loading(true)
    getClaims()
    LoadingService.loading(false)
  }, [])

  React.useEffect(() => {
    LoadingService.loading(true)
    getClaims()
    LoadingService.loading(false)
  }, [claims.search, claims.filter, claims.orderBy, claims.order, claims.limit, claims.page])

  const getClaims = () => {
    ClaimsVerifierSupervisorServices.getItems({
      category: claims.category,
      search: claims.search,
      sort: `${claims.orderBy}|${claims.order ? 'asc' : 'desc'}`,
      page: claims.page,
      limit: claims.limit,
      user_id: userId,
      filter: claims.filter
    })
      .then(res => {
        const { results, count, limit, page } = res.data.data
        results.map(claim => (claim.checked = false))
        setClaims(prevState => ({ ...prevState, data: results, count, limit, page }))
      })
      .catch(err => console.error('Error fetching records: ', err))
  }

  const handleItemSelect = (event, index) => {
    let data = [...claims.data]
    const count = event.target.checked ? 1 : -1
    data[index] = { ...data[index], checked: event.target.checked }
    setClaims(prevState => ({ ...prevState, data }))
    setClaimsCount(prevState => prevState + count)
  }

  const handleOnSearch = args => {
    const { field, value, match } = args
    setClaims(state => ({ ...state, filter: { ...state.filter, [field]: { value, match } } }))
  }

  const handleOnSearchAll = (args = '') => {
    setClaims(prevState => ({ ...prevState, search: args }))
  }

  const handleOnSubmit = params => {
    LoadingService.loading(true)
    const { user_id, message } = params
    const reassign = async () => {
      return Promise.all(
        claims.data.map(async claim => {
          if (claim.checked === true) {
            await ClaimsVerifierSupervisorServices.reassignClaim({
              data: claim.claim_number,
              user_id,
              message
            }).catch(err => console.error(err))
          }
        })
      )
    }
    reassign()
      .then(() => {
        handleUnselectAll()
        LoadingService.loading(false)
        getClaims()
      })
      .catch(err => console.error(('Error Assigning Claims: ', err)))
  }

  const handleOnPaginate = args => {
    setClaims(state => ({ ...state, ...args }))
  }

  const handleSelectAll = event => {
    let data = [...claims.data]
    const count = event.target.checked ? 1 : 0
    data.map(claim => {
      claim.checked = event.target.checked
    })
    setClaims(prevState => ({ ...prevState, data }))
    setClaimsCount(count > 0 ? data.length : 0)
  }

  const handleUnselectAll = () => {
    let data = [...claims.data]
    data.map(claim => {
      claim.checked = false
    })
    setClaims(prevState => ({ ...prevState, data }))
    setSelectStatus(false)
    setClaimsCount(0)
  }

  const handleTableRenderer = type => {
    if (type === 'headcell') {
      return (
        <TableRow>
          {selectStatus ? (
            <TableCell padding='checkbox'>
              <Checkbox color='default' onClick={event => handleSelectAll(event)} />
            </TableCell>
          ) : null}
          {headcell.map(headCell => (
            <TableCell key={headCell.sortField} id={headCell.id}>
              <TableSortLabel
                active={claims.orderBy === headCell.sortField}
                direction={claims.order ? 'asc' : 'desc'}
                onClick={event => handleOnPaginate({ orderBy: headCell.sortField, order: !claims.order })}
              >
                {headCell.name}
              </TableSortLabel>
              <TextField
                className={`${classes.search}`}
                onChange={event =>
                  handleOnSearch({
                    field: headCell.sortField,
                    value: event.target.value,
                    match: headCell.match
                  })
                }
              />
            </TableCell>
          ))}

          <TableCell align='center' />
        </TableRow>
      )
    } else if (type === 'body') {
      return claims.data.map((row, index) => (
        <TableRow key={index}>
          {selectStatus ? (
            <TableCell padding='checkbox'>
              <Checkbox checked={row.checked} color='default' onClick={event => handleItemSelect(event, index)} />
            </TableCell>
          ) : null}
          <TableCell id={`claim-number-${index}`}>{row.claim_number}</TableCell>
          <TableCell id={`date-received-${index}`}>{DateFormatter(row.date_received)}</TableCell>
          <TableCell id={`due-date-${index}`}>{DateFormatter(row.date_due)}</TableCell>
          <TableCell id={`facility-name-${index}`}>{row.facility_name}</TableCell>
          <TableCell id={`view-claim-${index}`}>
            <Link to={`/claims/view/${row.claim_number}`}>
              <Typography id={`analyze-claim-${index}`} variant='caption' className='text-link underline'>
                View
              </Typography>
            </Link>
          </TableCell>
        </TableRow>
      ))
    }
  }

  return (
    <div className={classes.root}>
      <Paper>
        <React.Fragment>
          <div className={classes['search-container']}>
            <div className={classes['reassign']}>
              <Button
                disabled={selectStatus}
                style={{ height: '48px', marginBottom: '17px' }}
                variant='contained'
                color='secondary'
                className='button-positive'
                onClick={() => {
                  setSelectStatus(true)
                }}
              >
                Reassign
              </Button>
            </div>
            <div className={classes['search']}>
              <SearchTextComponent
                id='search'
                onChange={e => handleOnSearchAll(e.target.value)}
                placeholder='Search for claim number or facility name'
              />
            </div>
          </div>
          <div className={classes['table-container']}>
            <Table
              TableContainerProps={{ style: { height: '100%', overflow: 'auto', position: 'relative' } }}
              TableProps={{ stickyHeader: true }}
              head={handleTableRenderer('headcell')}
              body={handleTableRenderer('body')}
              count={claims.count}
              page={claims.page}
              limit={claims.limit}
              rowsPerPageOptions={claims.rowsPerPageOptions}
              pagination
              onChange={args => handleOnPaginate(args)}
              cesFooter
            />
          </div>

          <ButtonFooter
            selectStatus={selectStatus}
            handleOnCancel={() => handleUnselectAll()}
            handleOnClose={() => props.history.push(`/`)}
            handleOnSubmit={handleOnSubmit}
            claimsCount={claimsCount}
          />
        </React.Fragment>
      </Paper>
    </div>
  )
}
