import React from 'react'
import Button from '@material-ui/core/Button'

// Components
import BatchDetails from '../../Components/BatchDetails/BatchDetails'
import ClaimListHeader from '../../Components/ClaimListHeader/ClaimListHeader'
import ClaimList from '../../Components/ClaimList/ClaimList'
// import Alert from 'SharedComponents/Alert/Alert'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
// import ProviderSchedule from '../../../ClaimsDetailsEncoder/Components/ProviderSchedule/ProviderSchedule'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import ClaimsEncoderSupervisorServices from 'Services/Api/ClaimsEncoderSupervisorServices/ClaimsEncoderSupervisorServices'

// Styles
import classes from './BatchPage.module.scss'

export default function BatchPage (props) {
  const [batchNumber, setBatchNumber] = React.useState(null)
  const [details, setDetails] = React.useState()
  const [claims, setClaims] = React.useState([])
  const [claimCount, setClaimCount] = React.useState(0)
  const [debounce, setDebounce] = React.useState(null)
  const [id, setId] = React.useState()

  const handleSearchChange = search => {
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        /*
         * @returnData is the sanitized input string
         * returned by the component ClaimsListHeader 's
         * inputChange props
         */
        getClaimsAndCount({ batch_number: batchNumber, search })
      }, 500)
    )
  }

  const getClaimsAndCount = ({ batch_number, search = '' }) => {
    ClaimsEncoderSupervisorServices.getClaims({ batch_number, params: { search } }).then(res => {
      const { results, count } = res.data.data

      setClaims(results)
      setClaimCount(count)
      LoadingService.loading(false)
    })
  }

  React.useEffect(() => {
    const batch_number = props.match.params._batchNumber
    const _id = props.match.params._id
    setId(_id)
    setBatchNumber(batch_number)

    ClaimsEncoderSupervisorServices.getEncoderBatch({ id: _id }, batch_number).then(res => {
      const { data } = res.data

      setDetails(data)
      LoadingService.loading(true)

      BreadcrumbsService.setSelectedItems(['dashboard', `assigned:${_id}`, `specificBatch:${batch_number}`])

      PageTitleService(`Batch No. ${batch_number}`, {
        text: 'In Progress',
        color: 'blue'
      })

      getClaimsAndCount({ batch_number: batch_number })
    })
  }, [props.match.params._batchNumber, props.match.params._id])

  return (
    <div className={classes.root}>
      {details && (
        <React.Fragment>
          <BatchDetails details={details} />
          <ClaimListHeader inputChange={handleSearchChange} count={claimCount} />
          <ClaimList items={claims} type={details.claim.type} />

          <div className={classes.footer}>
            <div className={classes.actions}>
              <Button
                variant='contained'
                color='primary'
                className='button-negative'
                onClick={() => props.history.push(`/batches/assigned/${id}`)}
              >
                Close
              </Button>
            </div>
          </div>
        </React.Fragment>
      )}
    </div>
  )
}
