import React from 'react'
import { Switch, Route } from 'react-router-dom'

// Pages of Batch Encoder
import BundlePage from './../Components/BatchEncoding/BundlePage/BundlePage'
import NewBundlePage from './../Components/BatchEncoding/NewBundlePage/NewBundlePage'

// Pages of Batch Encoder Supervisor
import Dashboard from './../Components/BatchEncodingSupervisory/Dashboard/Dashboard'
import CreatedBundlesPage from './../Components/BatchEncodingSupervisory/CreatedBundlesPage/CreatedBundlesPage'

// Pages of Recon User
import ClaimsViewer from '../ClaimsViewer'

// Store
import Store from './../ClaimsViewer/Store'
import { Provider } from 'react-redux'

// Services
import { SideBarService } from 'SharedComponents/SideBarComponent/SideBarComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import ClaimsReconServices from 'Services/Api/ClaimsReconServices'

const sideBarItems = [
  {
    label: 'Dashboard',
    icon: 'home',
    to: '/'
  }
]

const breadcrumbs = {
  dashboard: {
    text: 'Dashboard',
    to: '/'
  },
  specificBundle: {
    text: data => `Bundle No. ${data}`,
    to: data => `/bundles/${data}`
  },
  newBundle: {
    text: 'Create new bundle',
    to: '/bundles/create'
  },
  createdBundles: {
    text: data => `Created Bundles by Batch Encoder ${data}`,
    to: data => `/batch-encoder-work/${data}`
  },
  claims: {
    text: 'Claims',
    to: '/claims'
  },
  specificClaim: {
    text: data => data,
    to: data => `/claims/${data}`
  },
  recon: {
    text: 'Recon',
    to: ''
  }
}

const permissions = {
  base: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  diagnosis: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  procedures: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  hospital_items: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  professional_fees: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  tabs: {
    benefits: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    partner: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    utilization: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    contact: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    comments: {
      create: false,
      read: true,
      update: false,
      delete: false
    }
  },
  footer: {
    visible: true,
    enabled: true,
    reassign: {
      visible: true,
      enabled: true,
      options: { roles: ['CLAIMS_VERIFIER_SUPERVISOR'] }
    },
    analysis_sheet: { visible: true, enabled: true, options: {} },
    letter_of_advice: { visible: true, enabled: true, options: {} }
  }
}

function BatchEncoderSupervisorRoutes () {
  SideBarService.setItems(sideBarItems)
  BreadcrumbsService.setItems(breadcrumbs)

  return (
    <React.Fragment>
      <Switch>
        <Route path='/' exact component={Dashboard} />
        <Route path='/bundles/:bundle_number/create' exact component={NewBundlePage} />
        <Route path='/bundles/:bundle_number' exact component={BundlePage} />
        <Route path='/batch-encoder-work/:user_id' exact component={CreatedBundlesPage} />
        <Route path='/batch-encoder-work/:user_id/bundles/:bundle_number' exact component={BundlePage} />
        <Provider store={Store}>
          <Route
            path='/claims/recon/:claim_number'
            exact
            render={props => (
              <ClaimsViewer
                breadCrumbsList={['claims', `specificClaim:${props.match.params.claim_number}`, 'recon']}
                {...props}
                permissions={permissions}
                ApiService={ClaimsReconServices}
              />
            )}
          />
        </Provider>
      </Switch>
    </React.Fragment>
  )
}

export default BatchEncoderSupervisorRoutes
