import React from 'react'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

import { CurrencyFormatter, DateFormatter } from 'Utilities/TextFormatter'
import CustomTextField from 'SharedComponents/TextField/CustomTextField'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import EnhancedTable from 'SharedComponents/EnhancedTable/EnhancedTable'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import PaymentRequestServices from 'Services/Api/PaymentRequestServices'

// CSS
import classes from './SubmittedPaymentRequestPage.module.scss'

const headCells = [
  { id: 'batch-no', numeric: false, center: true, disablePadding: false, label: 'Batch No.' },
  { id: 'claim-type', numeric: false, center: true, disablePadding: false, label: 'Claim Type' },
  { id: 'total-rcs', numeric: false, center: true, disablePadding: false, label: 'Total No. of RCS' },
  { id: 'billed-amount', numeric: true, center: true, disablePadding: false, label: 'Billed Amount' }
]

export default function PaymentRequestPage (props) {
  const [bundle, setBundle] = React.useState(null)
  const [bundle_number, setBundleNumber] = React.useState(null)

  const [batches, setBatches] = React.useState({
    submitted: { data: [], header: {} }
  })

  React.useEffect(() => {
    const { bundle_number } = props.match.params
    PageTitleService(`Submmited PR: ${bundle_number}`)
    BreadcrumbsService.setSelectedItems(['dashboard', 'paymentRequest', `specificPaymentRequest:${bundle_number}`])

    setBundleNumber(bundle_number)
    PaymentRequestServices.getPaymentRequest({ bundle_number }).then(({ data: res }) => {
      const { data } = res
      setBundle(data)
      getBatches({ bundle_number, bundle: data })
    })
  }, [])

  const getBatches = ({ bundle_number, bundle }) => {
    // * Submitted Payment request
    PaymentRequestServices.getPaymentRequestBatches({ bundle_number, params: { category: 'submitted' } }).then(
      ({ data: res }) => resetBatches({ type: 'submitted', data: res.data, bundle })
    )
  }

  const resetBatches = ({ type, data, bundle }) => {
    const total_batches = data.length
    const total_rcs = data.reduce((acc, batch) => acc + batch.claims.length, 0)

    data = data.map(i => ({
      ...i,
      total_amount: i.claims.reduce((acc, claim) => acc + claim.utilization_amount, 0)
    }))

    const approved_amount = data.reduce((acc, batch) => acc + batch.total_amount, 0)

    setBatches(state => ({
      ...state,
      [type]: {
        ...state[type],
        data,
        header: {
          total_batches,
          total_rcs,
          pr_amount: approved_amount
        }
      }
    }))
  }
  const approved_by_uid = bundle ? bundle.approved_by_uid : ''

  return (
    <div className={classes.root}>
      {bundle && approved_by_uid ? (
        <React.Fragment>
          <Paper className={classes['text-fields']}>
            <Grid className={classes['sub-container']} container spacing={4}>
              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField readOnly label='Bundle No.' id='bundle-number' value={bundle.bundle_number} />
              </Grid>
              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField readOnly label='Total No. of RCS' id='total-rcs' value={bundle.total_rcs} />
              </Grid>
              <Grid item xs={12} />

              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField
                  readOnly
                  label='Date Received'
                  id='date-received'
                  value={DateFormatter(bundle.date.received)}
                />
              </Grid>
              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField readOnly label='Due Date' id='date-due' value={DateFormatter(bundle.date.due)} />
              </Grid>
              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField readOnly label='SOA No.' id='soa-number' value={bundle.soa.number} />
              </Grid>

              <Grid item xs={3} className={classes['input-container']}>
                <TextFieldCurrency
                  readOnly
                  label='Total SOA Amount'
                  id='total-soa'
                  value={CurrencyFormatter(bundle.soa.amount)}
                />
              </Grid>

              <Grid item xs={12} />

              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField readOnly label='Medical Partner ID' id='facility-id' value={bundle.facility.id} />
              </Grid>
              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField
                  readOnly
                  label='Medical Partner Name (Facility)'
                  id='facility-name'
                  value={bundle.facility.name}
                />
              </Grid>
              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField readOnly label='Medical Partner ID' id='doctor-id' value={bundle.doctor.id} />
              </Grid>
              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField
                  readOnly
                  label='Medical Partner Name (Doctor)'
                  id='doctor-name'
                  value={bundle.doctor.name}
                />
              </Grid>
            </Grid>
          </Paper>
          <Paper className={classes['table-container']}>
            <div className={classes.headers}>
              <Typography className={classes['total-batches']} align='center'>Total Batches: {batches.submitted.header.total_batches}</Typography>
              <Typography className={classes.header} />
              <Typography className={classes['total-rcs']} align='center'>Total RCS: {batches.submitted.header.total_rcs }</Typography>
              <Typography className={classes['total-pr-amount']} align='center'>
                  Total PR Amount: PHP{' '}
                <span id='total-pr-amount'>{CurrencyFormatter(batches.submitted.header.pr_amount)}</span>

              </Typography>
              {/* <Typography className={classes.header} align='center'><span className={classes['total-batches']}>Total Batches: {batches.submitted.header.total_batches}</span></Typography>
              <Typography className={classes.header} />
              <Typography className={classes.header} align='center'><span className={classes['total-rcs']}>Total RCS: {batches.submitted.header.total_rcs }</span></Typography>
              <Typography className={classes.header}>
                <span className={classes['total-pr-amount']} align='center'>
                  Total PR Amount: PHP{' '}
                  <span id='total-pr-amount'>{CurrencyFormatter(batches.submitted.header.pr_amount)}</span>
                </span>
              </Typography> */}
            </div>

            <EnhancedTable
              className={classes.table}
              headCells={headCells}
            >
              <TableBody>
                {batches.submitted.data.map((batch, index) => {
                  return (
                    <TableRow>
                      <TableCell align='center' id={`batch-number-${index}`}>
                        {batch.batch_number}
                      </TableCell>
                      <TableCell align='center' id={`claim-type-${index}`}>
                        {batch.type}
                      </TableCell>
                      <TableCell
                        align='center'
                        id={`total-rcs-${index}`}
                      >{batch.claims.length}</TableCell>
                      <TableCell align='center' id={`billed-amount-${index}`}>
                        {CurrencyFormatter(batch.total_amount)}
                      </TableCell>
                    </TableRow>
                  )
                })}
                {!batches.submitted.data.length && (
                  <TableRow style={{ height: 50 }}>
                    <TableCell colSpan={5} />
                  </TableRow>
                )}
              </TableBody>
            </EnhancedTable>
          </Paper>
          <div className={classes.buttonWrapper}>
            <Button
              variant='contained'
              color='primary'
              className='button-negative'
              onClick={() => props.history.push(`/payment-requests/${approved_by_uid}/list`)}
              id='cancel'
            >
              Close
            </Button>
          </div>

        </React.Fragment>
      ) : (
        ''
      )}
    </div>
  )
}
