import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'

// Components
import Table from 'SharedComponents/Table/Table'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'

// Styles
import classes from './ViewList.module.scss'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const headCell = {
  encoder: [
    { id: 'batch-number', name: ' Batch Number', sortField: 'batch_number' },
    { id: 'due-date', name: ' Due Date', sortField: 'date.due' },
    { id: 'facility-name', name: ' Medical Partner Name (Facility)', sortField: 'facility.name' }
  ],
  supervisor: [
    { id: 's-encoder-name', name: ' Encoder Name', sortField: 'encoder_name' },
    { id: 's-total-batches', name: ' Total Batches', sortField: 'total_batches' },
    { id: 's-total-rcs', name: ' Total No. of RCS', sortField: 'total_rcs' }
  ]
}

export default function CustomizedDialogs ({
  viewList,
  setViewList,
  onViewListChange,
  onItemClick,
  handleViewClick = () => {}
}) {
  const { category } = viewList

  const handleOnClose = () => {
    setViewList(p => ({ ...p, show: false }))
  }

  const [debounce, setDebounce] = React.useState(null)

  const handleSearchChange = e => {
    const search = e.target.value
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        setViewList(p => ({ ...p, search }))
        onViewListChange({ search, page: 1 })
      }, 500)
    )
  }

  const handleOnSort = orderBy => e => {
    setViewList(p => ({ ...p, orderBy, order: !viewList.order }))
    onViewListChange({ sort: `${orderBy}|${viewList.order ? 'asc' : 'desc'}` })
  }

  const handleTableRenderer = type => {
    if (type === 'headcell' && category !== 'claim-encoder') {
      return (
        <TableRow>
          {headCell.encoder.map(headCell => (
            <TableCell key={headCell.sortField} id={headCell.id}>
              <TableSortLabel
                active={viewList.orderBy === headCell.sortField}
                direction={viewList.order ? 'asc' : 'desc'}
                onClick={handleOnSort(headCell.sortField)}
              >
                {headCell.name}
              </TableSortLabel>
            </TableCell>
          ))}

          <TableCell align='center' />
        </TableRow>
      )
    } else if (type === 'headcell') {
      return (
        <TableRow>
          {headCell.supervisor.map(headCell => (
            <TableCell
              align={headCell.id === 's-encoder-name' ? 'inherit' : 'center'}
              key={headCell.sortField}
              id={headCell.id}
            >
              <TableSortLabel
                active={viewList.orderBy === headCell.sortField}
                direction={viewList.order ? 'asc' : 'desc'}
                onClick={handleOnSort(headCell.sortField)}
              >
                {headCell.name}
              </TableSortLabel>
            </TableCell>
          ))}

          <TableCell align='center' />
        </TableRow>
      )
    } else if (type === 'body' && category !== 'claim-encoder') {
      return viewList.data.map((row, index) => (
        <TableRow key={row.batch_number}>
          <TableCell id={`batch-number-${index}`}>{row.batch_number}</TableCell>
          <TableCell id={`due-date-${index}`}>{DateFormatter(row.date.due)}</TableCell>
          <TableCell id={`facility-name=${index}`}>{row.facility.name}</TableCell>
          <TableCell
            id={
              category === 'open'
                ? `encode-batch-${index}`
                : category === 'my'
                ? `update-batch-${index}`
                : `view-batch-${index}`
            }
          >
            <Typography
              variant='caption'
              className='text-link underline'
              onClick={() => onItemClick({ category, batch_number: row.batch_number })}
            >
              {category === 'open' && 'Encode'}
              {category === 'my' && 'Update'}
              {category === 'completed' && 'View'}
            </Typography>
          </TableCell>
        </TableRow>
      ))
    } else if (type === 'body') {
      return viewList.data.map((row, index) => (
        <TableRow key={row.id}>
          <TableCell id={`batch-number-${index}`}>{row.username}</TableCell>
          <TableCell align='center' id={`date-received-${index}`}>
            {row.total_batches}
          </TableCell>
          <TableCell align='center' id={`due-date-${index}`}>
            {row.total_rcs}
          </TableCell>
          <TableCell id={`view-encoder-${index}`}>
            <Typography variant='caption' className='text-link underline' onClick={() => handleViewClick(row.id)}>
              View
            </Typography>
          </TableCell>
        </TableRow>
      ))
    }
  }

  return (
    <Dialog
      fullWidth
      maxWidth='lg'
      onClose={handleOnClose}
      aria-labelledby='customized-dialog-title'
      open={viewList.show}
      className={classes.root}
      PaperProps={{ style: { height: '100%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <Typography variant='h6' className={classes.title}>
            {viewList.title}
          </Typography>
          <IconButton aria-label='close' className={classes.button} onClick={handleOnClose}>
            <CloseIcon />
          </IconButton>
        </div>
      </DialogTitle>

      <DialogContent dividers className={classes.content}>
        <div className={classes.search}>
          <SearchTextComponent
            id='search'
            onChange={handleSearchChange}
            placeholder='Search for batch number, date or facility name'
          />
        </div>
        <Table
          TableContainerProps={{ style: { height: '100%', overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          head={handleTableRenderer('headcell')}
          body={handleTableRenderer('body')}
          count={viewList.count}
          page={viewList.page}
          limit={viewList.limit}
          rowsPerPageOptions={[10, 15, 20]}
          onChange={onViewListChange}
        />
      </DialogContent>
    </Dialog>
  )
}
