import Button from '@material-ui/core/Button'
import CloseIcon from '@material-ui/icons/Close'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogTitle from '@material-ui/core/DialogTitle'
import IconButton from '@material-ui/core/IconButton'
import React from 'react'
import Typography from '@material-ui/core/Typography'

// Components
import DropDown from 'SharedComponents/DropDown/DropDown'
import TextField from 'SharedComponents/TextField/CustomTextField'

// style
import classes from './AssignDialog.module.scss'

// Services
import UserManagementServices from 'Services/Api/UserManagementServices'

export default function AssignDialog (props) {
  const { dialog, handleOnSubmit = () => {}, setDialog } = props

  const [reassignData, setReassignData] = React.useState({
    user_id: '',
    message: ''
  })
  const [dropDownUsers, setDropDownUsers] = React.useState([])

  React.useEffect(() => {
    UserManagementServices.getClaimsAnalyst()
      .then(res => {
        const _data = res.data
        const data = _data.map(user => {
          return { value: user._id, text: `${user.first_name} ${user.last_name}` }
        })
        setDropDownUsers(prevState => [...prevState, ...data])
      })
      .catch(err => console.error('Failed fetching Claims Verifiers: ', err))
  }, [])

  const onDropDownChanged = params => {
    setReassignData(prevState => ({ ...prevState, user_id: params }))
  }

  const onTextFieldChanged = params => {
    setReassignData(prevState => ({ ...prevState, message: params }))
  }

  return (
    <div>
      <React.Fragment>
        <Dialog
          open={dialog.assign}
          onClose={() => setDialog(state => ({ ...state, assign: false }))}
          PaperProps={{ style: { height: 'auto', display: 'flex' } }}
          className={classes['reassign']}
          fullWidth
          maxWidth='md'
          aria-labelledby='customized-dialog-title'
        >
          <DialogTitle disableTypography className={classes['header-container']}>
            <div className={classes.header}>
              <Typography variant='h6' className={classes.title}>
                Notes
              </Typography>
              <IconButton
                aria-label='close'
                className={classes.button}
                onClick={() => setDialog(state => ({ ...state, assign: false }))}
              >
                <CloseIcon />
              </IconButton>
            </div>
          </DialogTitle>

          <DialogContent dividers className={classes['content']}>
            <div className={classes.input}>
              <TextField
                className={classes.comment}
                multiline
                rows={5}
                id='reassign-notes'
                value={reassignData.message}
                autoFocus
                fullWidth
                placeholder='Compose a Message'
                onChange={e => onTextFieldChanged(e.target.value)}
              />
            </div>
            <div className={classes.footer}>
              <div className={classes.assign}>
                <Typography variant='subtitle2' className={classes.to}>
                  {dropDownUsers && dropDownUsers.length > 0 ? 'Assign To' : 'No Users'}
                </Typography>
                <DropDown
                  id='reassign-to'
                  items={dropDownUsers}
                  value={reassignData.user_id}
                  onChange={e => onDropDownChanged(e.target.value)}
                  disableComponents={!!(dropDownUsers && dropDownUsers.length === 0)}
                />
              </div>

              <div className={classes.action}>
                <Button
                  onClick={() => setDialog(state => ({ ...state, assign: false }))}
                  variant='contained'
                  color='primary'
                  className='button-negative'
                >
                  Cancel
                </Button>
                <Button
                  onClick={() => setDialog(state => ({ ...state, alert: true }))}
                  variant='contained'
                  color='primary'
                  className='button-positive'
                >
                  Submit
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>
        <Dialog
          open={dialog.alert}
          onClose={() => setDialog(state => ({ ...state, alert: false }))}
          aria-labelledby='alert-dialog-title'
          aria-describedby='alert-dialog-description'
          maxWidth='xs'
        >
          <DialogTitle className={classes['alert-title']}>
            <Typography variant='inherit'>Assign Claim to Claim Verifier</Typography>
          </DialogTitle>

          <DialogContent className={classes['message-content']}>
            <div className={classes['message']}>
              <Typography className={classes['message-child']} variant='body1'>
                Are you sure you want to submit the Claim to the selected Claim Analyst?
              </Typography>
              <Typography variant='body1'>
                This Claim will be sent to the assigned Claim Analyst for processing.
              </Typography>
            </div>
          </DialogContent>

          <DialogActions className={classes['footer-action']}>
            <React.Fragment>
              <Button
                onClick={() => setDialog(state => ({ ...state, alert: false }))}
                variant='contained'
                color='primary'
                className='button-negative'
              >
                Cancel
              </Button>
              <Button
                onClick={() => {
                  handleOnSubmit(reassignData)
                  setDialog(state => ({ ...state, alert: false, assign: false }))
                }}
                variant='contained'
                color='primary'
                className='button-positive'
              >
                Submit
              </Button>
            </React.Fragment>
          </DialogActions>
        </Dialog>
      </React.Fragment>
    </div>
  )
}
