const initialValue = {
  notes: [],
  assignee: { id: '', name: '' },
  note: ''
}

const reassignReducer = (state = initialValue, action) => {
  switch (action.type) {
    case 'RESET_REASSIGN_NOTES':
      return initialValue

    case 'SET_REASSIGN_NOTES':
      return (() => {
        return {
          ...state,
          note: action.payload
        }
      })()

    case 'SET_REASSIGN_ASSIGNEE':
      return (() => {
        const { id, name } = action.payload
        return {
          ...state,
          assignee: { id, name }
        }
      })()

    default:
      return state
  }
}

export default reassignReducer
