import React from 'react'
import { Provider } from 'react-redux'
import { Switch, Route } from 'react-router-dom'

// Pages of Claims Details Encoder
import Dashboard from './Pages/Dashboard/Dashboard'

// TODO Need to create common components for Claims Encoder and Supervisor Encoder
// TODO Imported from Claims Encoder
import BatchesPage from '../ClaimsDetailsEncoder/Pages/BatchesPage/BatchesPage'
import BatchPage from '../ClaimsDetailsEncoder/Pages/BatchPage/BatchPage'
import ClaimPage from '../ClaimsDetailsEncoder/Pages/ClaimPage/ClaimPage'
import CreatedBatchesPage from './Pages/CreatedBatchesPage/CreatedBatchesPage'
import BatchePage from './Pages/BatchPage/BatchPage'

// Services
import { SideBarService } from 'SharedComponents/SideBarComponent/SideBarComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Store
import Store from '../ClaimsDetailsEncoder/Store'

const sideBarItems = [
  {
    label: 'Dashboard',
    icon: 'home',
    to: '/'
  },
  {
    label: 'Batches',
    icon: 'box-open',
    to: '/batches'
  }
]

const breadcrumbs = {
  dashboard: {
    text: 'Dashboard',
    to: '/'
  },
  batches: {
    text: 'Batches',
    to: '/batches'
  },
  assigned: {
    text: data => 'Created Batches',
    to: data => `/batches/assigned/${data}`
  },

  specificBatch: {
    text: data => data,
    to: data => `/batches/${data}`
  },
  addClaim: {
    text: 'Add new claim',
    to: ''
  },
  updateClaim: {
    text: 'Update Claim',
    to: ''
  }
}

function ClaimsDetailsEncoderSupervisorRoutes () {
  SideBarService.setItems(sideBarItems)
  BreadcrumbsService.setItems(breadcrumbs)

  return (
    <Provider store={Store}>
      <Switch>
        <Route path='/' exact component={Dashboard} />
        <Route path='/batches' exact component={BatchesPage} />
        <Route path='/batches/:_batchNumber' exact component={BatchPage} />
        <Route path='/batches/assigned/:_id' exact component={CreatedBatchesPage} />
        <Route path='/batches/assigned/:_id/:_batchNumber' exact component={BatchePage} />
        <Route path='/batches/:_batchNumber/claims/add' exact render={props => <ClaimPage {...props} isAdd />} />
        <Route path='/batches/:_batchNumber/claims/:_claimNumber' exact component={ClaimPage} />
      </Switch>
    </Provider>
  )
}

export default ClaimsDetailsEncoderSupervisorRoutes
