export default function UtilizationLedgerReport (props) {
  const [claims, setClaims] = React.useState({
    data: [],
    search: '',
    range: { from: new Date(), to: new Date() },
    filter: {},
    orderBy: 'claim_number',
    order: true,
    page: 1,
    limit: 10,
    count: 0,
    rowsPerPageOptions: [10, 15, 20]
  })

  // const [allClaims, setAllClaims] = React.useState({
  //   data: [],
  //   columns: columns.map(value => ({
  //     name: value.name,
  //     type: value.type
  //   }))
  // })

  const handleOnPaginate = args => {
    setClaims(state => ({ ...state, ...args }))
  }

  const handleOnFilter = args => {
    const { field, value, match } = args
    setClaims(state => ({ ...state, filter: { ...state.filter, [field]: { value, match } } }))
  }

  const fetchLedger = () => {
    ClaimsServices.utilizationLegder({
      search: claims.search,
      range: claims.range,
      filter: claims.filter,
      sort: `${claims.orderBy}|${claims.order ? 'asc' : 'desc'}`,
      page: claims.page,
      limit: claims.limit
    }).then(res => {
      const { data } = res.data
      const { results = [], count = 0 } = data
      const _claims = results.map(claim => ({
        ...claim,
        date_admission_from: DateFormatter(claim.date_admission_from),
        date_admission_to: DateFormatter(claim.date_admission_to),
        date_service: DateFormatter(claim.date_service),
        date_service_from: DateFormatter(claim.date_service_from),
        date_service_to: DateFormatter(claim.date_service_to),
        contract_from: DateFormatter(claim.contract_from),
        contract_to: DateFormatter(claim.contract_to)
        // date_admission_from: DateFormatter(claim.date_admitted),
        // date_discharged: DateFormatter(claim.date_discharged),
        // date_issued: DateFormatter(claim.date_issued),
        // diagnosis: claim.diagnosis && claim.diagnosis.length > 0 ? claim.diagnosis.shift().description : null
      }))
      setClaims(state => ({ ...state, data: _claims, count }))
    })
  }

  const fetchCSV = () => {
    ClaimsServices.utilizationLegder({
      search: claims.search,
      range: claims.range,
      filter: claims.filter,
      sort: `${claims.orderBy}|${claims.order ? 'asc' : 'desc'}`,
      page: claims.page,
      limit: claims.count > 0 ? claims.count : claims.rowsPerPageOptions[0]
    }).then(res => {
      const { data } = res.data
      const { results = [] } = data
      const _claims = results.map(claim => ({
        ...claim,
        date_admission_from: DateFormatter(claim.date_admission_from),
        date_admission_to: DateFormatter(claim.date_admission_to),
        date_service: DateFormatter(claim.date_service),
        date_service_from: DateFormatter(claim.date_service_from),
        date_service_to: DateFormatter(claim.date_service_to),
        contract_from: DateFormatter(claim.contract_from),
        contract_to: DateFormatter(claim.contract_to)
      }))

      const rows = _claims.map(claim => {
        return columns.map(col => {
          if (col.type === 'array') console.log(formatData({ type: col.type, source: claim, field: col.data }))
          return formatData({ type: col.type, source: claim, field: col.data })
        })
      })
      // console.log(rows)

      const header = columns.map(c => c.label)

      ArrayToCSV({ fileName: 'Utilization Ledger Report', header, rows })
    })
  }

  React.useEffect(() => {
    PageTitleService('Utilization Ledger Report')
    BreadcrumbsService.setSelectedItems(['reports'])
  }, [])

  React.useEffect(() => {
    fetchLedger()
    // claim.range is triggered manually via Filter button
  }, [claims.search, claims.filter, claims.orderBy, claims.order, claims.limit, claims.page])

  const formatData = ({ type = null, source, field }) => {
    let output = ''
    if (type === 'date') {
      output = DateFormatter(source[field])
    } else if (type === 'array') {
      const [root, property] = field.split('.')
      output = source[root] ? source[root].map(f => f[property]).join(`${'\r\n'}${'\r\n'}`) : ''
    } else {
      output = source[field] ? source[field] : ''
    }

    return output && output !== '' && output !== `${'\r\n'}${'\r\n'}` ? output : 'N/A'
  }

  return (
    <Paper className={classes.root}>
      <div className={classes.header}>
        <Typography variant='h6'>Utilization Date Range</Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <p>From: </p>
              <DatePicker
                id='filter-date-from'
                value={claims.range.from}
                onChange={event =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      from: DateFormatter(event)
                    }
                  }))
                }
              />
            </div>
            <div className={classes.input}>
              <p>To: </p>
              <DatePicker
                id='filter-date-to'
                value={claims.range.to}
                onChange={event =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      to: DateFormatter(event)
                    }
                  }))
                }
              />
            </div>
            <Button
              className={`button-positive ${classes.submit}`}
              onClick={() => fetchLedger()}
              variant='contained'
              color='primary'
            >
              Filter
            </Button>
          </div>
          <div className={classes.action}>
            <Button id='generate-report' onClick={() => fetchCSV()}>
              <GetAppIcon fontSize='small' />
              <Typography variant='caption' className='bold text-link'>
                Generate Report
              </Typography>
            </Button>
          </div>
        </div>
      </div>
      <div className={classes.body}>
        <div className={classes.table}>
          <Table
            TableContainerProps={{ style: { maxHeight: '60vh', overflow: 'auto', position: 'relative' } }}
            TableProps={{ stickyHeader: true }}
            head={
              <TableRow>
                {columns.map((headCell, index) => (
                  <TableCell key={`table-header-${index}`}>
                    <TableSortLabel
                      onClick={event => handleOnPaginate({ orderBy: headCell.data, order: !claims.order })}
                    >
                      {headCell.label}
                    </TableSortLabel>
                    {headCell.search && (
                      <div className={classes.input}>
                        {headCell.type && headCell.type === 'date' ? (
                          <DatePicker
                            noMargin
                            id={`${headCell.data}`}
                            onChange={event =>
                              handleOnFilter({
                                field: headCell.data,
                                value: DateFormatter(event),
                                match: headCell.match
                              })
                            }
                          />
                        ) : (
                          <TextField
                            noMargin
                            onChange={event =>
                              handleOnFilter({
                                field: headCell.data,
                                value: event.target.value,
                                match: headCell.match
                              })
                            }
                          />
                        )}
                      </div>
                    )}
                  </TableCell>
                ))}
              </TableRow>
            }
            body={
              claims.data &&
              claims.data.map((claim, claimIndex) => (
                <TableRow className={classes['table-row']} key={`table-item-${claimIndex}`}>
                  {columns.map((col, colIndex) => (
                    <TableCell key={`table-cell-${claimIndex}-${colIndex}`}>
                      {formatData({ type: col.type, source: claim, field: col.data })}
                    </TableCell>
                  ))}
                </TableRow>
              ))
            }
            pagination
            count={claims.count}
            page={claims.page}
            limit={claims.limit}
            rowsPerPageOptions={claims.rowsPerPageOptions}
            onChange={args => handleOnPaginate(args)}
          />
        </div>
      </div>
    </Paper>
  )
}
