import instance from './../instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
import ClaimsAdjudicationServices from './ClaimsAdjudicationServices'
import MedicalCollectiblesServices from './MedicalCollectiblesServices'
const axios = instance('/c/cvs')

class index extends ClaimsAdjudicationServices {
  // MOCK ENDPOINT
  static getClaims (params) {
    return axios({
      url: '/claims/all',
      method: 'GET',
      params
    })
  }

  static getClaimCount (params) {
    return axios({
      url: `/claims/all/count`,
      method: 'GET',
      params
    })
  }

  static getItems (params) {
    return axios({
      url: '/claims',
      method: 'GET',
      params
    })
  }

  static getItemCount (params) {
    return axios({
      url: `/claims/count`,
      method: 'GET',
      params
    })
  }
  static getUsers (params) {
    return axios({
      url: `/lookups/users`,
      method: 'GET',
      params,
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static getClaimsItems (params) {
    return axios({
      url: '/subordinate/claims',
      method: 'GET',
      params
    })
  }
  static getClaimsBySubbordinateCount (params) {
    return axios({
      url: '/subordinate/claims/count',
      method: 'GET',
      params
    })
  }

  static reassignClaim (params) {
    return axios({
      url: `/subordinate/claims/reassign`,
      method: 'POST',
      params
    })
  }
}

index.ClaimsAdjudicationServices = ClaimsAdjudicationServices
index.MedicalCollectiblesServices = MedicalCollectiblesServices
export default index
