import React from 'react'
import Typography from '@material-ui/core/Typography'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ReimbursementList/ReimbursementList'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const ReimbursementClaimTile = props => {
  const { services } = props
  const [claims, setClaims] = React.useState({
    count: 0,
    data: []
  })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'claim_number',
    order: true,
    category: 'reimbursement',
    filter: {}
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    Promise.all([
      services.getItems({
        category: viewList.category,
        limit: viewList.limit,
        sort: `${viewList.orderBy}|${viewList.order ? 'asc' : 'desc'}`,
        range: 'this-week',
        filter: viewList.filter
      }),
      services.getItemCount({ claim_type: 'Reimbursement' })
    ]).then(values => {
      const [claimResponse, claimCountResponse] = values
      const { results } = claimResponse.data.data
      setClaims(prevState => ({
        ...prevState,
        data: results,
        count: claimCountResponse.data.data
      }))
    })
  }, [])

  const handleOnReview = claim_number => {
    props.history.push(`/claims/reimbursement/${claim_number}`)
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      filter: viewList.filter,
      ...params
    }

    services.getItems(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnClickItemViewList = ({ claim_number }) => {
    handleOnReview(claim_number)
  }

  const handleOnShowViewList = args => () => {
    let title = 'Reimbursement Claims'
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`
    }

    services.getItems(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })

    setViewList({ ...initialValue, show: true, title })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='reimbursement-claims'
        count={{ color: 3, value: claims.count, id: 'reimbursement-claims-count' }}
        sizes={{ sm: 12 }}
        title='Reimbursement Claims'
        isFilterShown={false}
        viewList={
          <Typography id='rmc-view-list' variant='caption' className='bold text-link' onClick={handleOnShowViewList()}>
            View List
          </Typography>
        }
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              <TableCell id='rmc-claim-no'>Claim No.</TableCell>
              <TableCell id='rmc-due-date'>Due Date</TableCell>
              <TableCell id='rmc-account-no'>Account No.</TableCell>
              <TableCell id='rmc-member-name'>Member Name</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {claims.data.map((item, index) => (
              <TableRow key={`rmc-claim-list-${index}`}>
                <TableCell id={`rmc-claim-no-${index}`}>{item.claim_number}</TableCell>
                <TableCell id={`rmc-due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
                <TableCell id={`rmc-account-no-${index}`}>{item.member.account_number}</TableCell>
                <TableCell id={`rmc-member-name-${index}`}>{`${item.member.first_name} ${item.member.middle_name |
                  ''} ${item.member.last_name}`}</TableCell>
                <TableCell id={`rmc-view-claim-${index}`}>
                  <Typography
                    id={`view-claim-${index}`}
                    variant='caption'
                    className={`text-link underline`}
                    onClick={() => handleOnReview(item.claim_number)}
                  >
                    View
                  </Typography>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        onItemClick={handleOnClickItemViewList}
      />
    </React.Fragment>
  )
}

ReimbursementClaimTile.displayName = 'DashboardTileComponent'

export default ReimbursementClaimTile
