import React from 'react'
import Typography from '@material-ui/core/Typography'

// Components
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'

// Styles
import classes from './ClaimListHeader.module.scss'

export default function ClaimListHeader (props) {
  const { count, inputChange = null } = props
  const handleChange = event => {
    if (typeof inputChange === 'function') {
      inputChange(event.target.value)
    }
  }

  return (
    <div className={classes.root}>
      <div className={classes.title}>
        <Typography variant='subtitle2'>Claims ({count})</Typography>
      </div>

      <div className={classes.wrapper}>
        <SearchTextComponent onChange={handleChange} />
      </div>
    </div>
  )
}
