import instance from './instance'
// import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/c/bes')

export default class BatchEncodingSupervisoryServices {
  static getBatchEncoderWork (params) {
    return axios({
      url: '/batch-encoder-work',
      method: 'GET',
      params
    })
  }

  static getBundles ({ user_id, params }) {
    return axios({
      url: `/batch-encoder-work/${user_id}/bundles`,
      method: 'GET',
      params
    })
  }

  static reassignBundles ({ user_id, data }) {
    return axios({
      url: `/batch-encoder-work/${user_id}/bundles/reassign`,
      method: 'POST',
      data
    })
  }
}
