import React from 'react'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import DialogTitle from '@material-ui/core/DialogTitle'
import CloseIcon from '@material-ui/icons/Close'
import IconButton from '@material-ui/core/IconButton'
import DialogActions from '@material-ui/core/DialogActions'
import Typography from '@material-ui/core/Typography'

// Components
import DropDown from 'SharedComponents/DropDown/DropDown'
import TextField from 'SharedComponents/TextField/CustomTextField'

// style
import classes from './AssignDialog.module.scss'

// Validator
import ReassignValidator from './ReassignValidator'

export default function AssignDialog (props) {
  const { show, setShow, services, onSubmit } = props

  const [reassign, setReassign] = React.useState({})
  const [showAlert, setShowAlert] = React.useState()
  const [users, setUsers] = React.useState([])

  const [reassignErrors, setReassignErrors] = React.useState({})

  React.useEffect(() => {
    setReassign({})
    setReassignErrors({})
    setUsers([])
    setShowAlert()

    if (show) {
      services.getUsers('BATCH_ENCODER').then(({ data: response }) => {
        setUsers(
          response.map(item => ({
            value: item._id,
            text: `${item.first_name} ${item.last_name}`
          }))
        )
      })
    }
  }, [show])

  const validate = () => {
    const { error } = ReassignValidator(reassign)
    setReassignErrors({ ...error })
    return !!error
  }

  const handleOnSubmit = () => {
    if (!validate()) setShowAlert(true)
  }

  const handleOnClose = () => {
    setShow()
  }

  const handleOnAlertConfirm = () => {
    setShow()
    onSubmit({ ...reassign })
  }

  return (
    <div>
      <React.Fragment>
        <Dialog
          open={show || false}
          onClose={handleOnClose}
          PaperProps={{ style: { height: 'auto', display: 'flex' } }}
          className={classes['reassign']}
          fullWidth
          maxWidth='md'
          aria-labelledby='customized-dialog-title'
        >
          <DialogTitle disableTypography className={classes['header-container']}>
            <div className={classes.header}>
              <Typography variant='h6' className={classes.title}>
                Notes
              </Typography>
              <IconButton aria-label='close' className={classes.button} onClick={handleOnClose}>
                <CloseIcon />
              </IconButton>
            </div>
          </DialogTitle>

          <DialogContent dividers className={classes['content']}>
            <div className={classes.input}>
              <TextField
                className={classes.comment}
                multiline
                rows={5}
                onChange={event => {
                  const note = event.target.value
                  setReassign(state => ({
                    ...state,
                    note
                  }))
                }}
                id=''
                value={reassign.note}
                autoFocus
                fullWidth
                placeholder='Compose a Note'
                error={reassignErrors.note}
              />
            </div>
            <div className={classes.footer}>
              <div className={classes.assign}>
                <Typography variant='subtitle2' className={classes.to}>
                  {users && users.length > 0 ? 'Assign To' : 'No Users'}
                </Typography>
                <DropDown
                  id='dropdown-assign-to'
                  items={users}
                  value={reassign.assignee || ''}
                  error={reassignErrors.assignee}
                  onChange={event =>
                    setReassign(state => ({
                      ...state,
                      assignee: event.target.value
                    }))
                  }
                  disableComponents={!!(users && users.length === 0)}
                />
              </div>

              <div className={classes.action}>
                <Button onClick={handleOnClose} variant='contained' color='primary' className='button-negative'>
                  Cancel
                </Button>
                <Button onClick={handleOnSubmit} variant='contained' color='primary' className='button-positive'>
                  Submit
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>
        <Dialog
          open={showAlert || false}
          onClose={setShowAlert}
          aria-labelledby='alert-dialog-title'
          aria-describedby='alert-dialog-description'
          maxWidth='xs'
        >
          <DialogTitle className={classes['alert-title']}>
            <Typography variant='inherit'>Assign Bundle/SOA to Batch Encoder</Typography>
          </DialogTitle>

          <DialogContent className={classes['message-content']}>
            <div className={classes['message']}>
              <Typography className={classes['message-child']} variant='body1'>
                Are you sure you want to submit the Bundle/SOA to the selected Batch Encoder?
              </Typography>
              <Typography variant='body1'>
                This Bundle/SOA will be sent to the assigned Batch Encoder for processing.
              </Typography>
            </div>
          </DialogContent>

          <DialogActions className={classes['footer-action']}>
            <React.Fragment>
              <Button onClick={() => setShowAlert()} variant='contained' color='primary' className='button-negative'>
                Cancel
              </Button>
              <Button onClick={handleOnAlertConfirm} variant='contained' color='primary' className='button-positive'>
                Submit
              </Button>
            </React.Fragment>
          </DialogActions>
        </Dialog>
      </React.Fragment>
    </div>
  )
}
