import ClaimsAdjudicationServices from '../ClaimsAdjudicationServices'
import instance from '../instance'
const axios = instance('/c/cvs')

export default class ClaimsReimbursementServices extends ClaimsAdjudicationServices {
  static getItems (params) {
    return axios({
      url: `/claims/medical-collectibles/claims`,
      method: 'GET',
      params
    })
  }

  static getItemCount (params) {
    return axios({
      url: `/claims/medical-collectibles/count`,
      method: 'GET',
      params
    })
  }
}
