export const resetReassign = payload => {
  return {
    type: 'RESET_REASSIGN_NOTES',
    payload
  }
}

export const setReassignNotes = payload => {
  return {
    type: 'SET_REASSIGN_NOTES',
    payload
  }
}

export const setReassignAssignee = payload => {
  return {
    type: 'SET_REASSIGN_ASSIGNEE',
    payload
  }
}
