import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Button from '@material-ui/core/Button'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from './../ViewList2/ViewList'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columns = [
  { id: 'bundle-number', name: ' Bundle Number', sortField: 'bundle_number' },
  { id: 'due-date', name: ' Due Date', sortField: 'date.due' },
  { id: 'facility-name', name: ' Medical Partner Name (Facility)', sortField: 'facility.name' }
]

const CreatedBundlesTile = props => {
  const { services, sizes } = props
  const [items, setItems] = React.useState({ count: 0, data: [] })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  const generateBundleNumber = () => {
    services.createBundle().then(res => {
      const { data } = res.data
      props.history.push(`/bundles/${data}/create`)
    })
  }

  React.useEffect(() => {
    Promise.all([
      services.getBundles({ category: 'created', limit: 10, sort: 'date.due|desc', range: 'this-week' }),
      services.getBundleCount({ category: 'created' })
    ]).then(values => {
      const [batchResponse, batchCountResponse] = values
      setItems({ data: batchResponse.data.data.results, count: batchCountResponse.data.data })
    })
  }, [])

  const handleFilterChange = value => {
    services
      .getBundles({ category: 'created', range: value, limit: 10, sort: 'date.due|desc' })
      .then(({ data: res }) => {
        const { data } = res
        setItems(prevState => ({ ...prevState, data: data.results }))
      })
  }

  const handleOnShowViewList = () => {
    setViewList({ ...initialValue, show: true, title: 'Created Bundles' })
    services.getBundles({ category: 'created', limit: 10, sort: 'date.due|desc' }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: 'created',
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    services.getBundles(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  return (
    <React.Fragment>
      <DashboardTile
        count={{ color: 3, value: items.count, id: 'created-count' }}
        sizes={sizes}
        title='Created Bundles'
        id='created-bundles'
        viewList={
          <Typography
            variant='caption'
            className='bold text-link'
            onClick={handleOnShowViewList}
            id='created-view-list'
          >
            View List
          </Typography>
        }
        actionHeader={
          <Button
            variant='contained'
            color='primary'
            className='button-neutral'
            onClick={generateBundleNumber}
            id='create-new-bundle'
          >
            Create New Bundle
          </Button>
        }
        onFilterChange={handleFilterChange}
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              {columns.map(item => (
                <TableCell key={`created-${item.id}`} id={`created-${item.id}`}>
                  {item.name}
                </TableCell>
              ))}
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {items.data.map((item, index) => (
              <TableRow key={item.bundle_number}>
                <TableCell id={`created-bundle-no-${index}`}>{item.bundle_number}</TableCell>
                <TableCell id={`created-due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
                <TableCell id={`created-facility-name-${index}`}>{item.facility.name}</TableCell>
                <TableCell>
                  <Link to={`/bundles/${item.bundle_number}`}>
                    <Typography variant='caption' className='text-link underline' id={`update-bundle-${index}`}>
                      Update
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewList
        columns={columns}
        searchTextPlaceHolder='Search for bundle number, date or facility name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        body={_data =>
          _data.map((row, index) => (
            <TableRow key={row.bundle_number}>
              <TableCell id={`bundle-number-${index}`}>{row.bundle_number}</TableCell>
              <TableCell id={`due-date-${index}`}>{DateFormatter(row.date.due)}</TableCell>
              <TableCell id={`facility-name-${index}`}>{row.facility.name}</TableCell>
              <TableCell id={`update-bundle-${index}`}>
                <Link to={`/bundles/${row.bundle_number}`}>
                  <Typography variant='caption' className='text-link underline'>
                    Update
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

CreatedBundlesTile.displayName = 'DashboardTileComponent'

export default CreatedBundlesTile
