import React from 'react'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import DialogTitle from '@material-ui/core/DialogTitle'
import CloseIcon from '@material-ui/icons/Close'
import IconButton from '@material-ui/core/IconButton'
import DialogActions from '@material-ui/core/DialogActions'
import Typography from '@material-ui/core/Typography'

// Components
import DropDown from 'SharedComponents/DropDown/DropDown'
import TextField from 'SharedComponents/TextField/CustomTextField'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { setReassignAssignee, setReassignNotes } from '../../../ClaimsDetailsEncoder/Store/actions'

// style
import classes from './AssignDialog.module.scss'

// Validator
import ReassignValidator from './ReassignValidator'

export default function AssignDialog (props) {
  const { dialog, setDialog, dropDownUsers = [], handleOnSubmitReassign } = props
  const dispatch = useDispatch()
  const reassign = useSelector(state => state.reassign)

  const [reassignErrors, setReassignErrors] = React.useState({
    note: '',
    assignee: ''
  })

  const validate = () => {
    const { error } = ReassignValidator({
      note: reassign.note,
      assignee: reassign.assignee.id
    })

    setReassignErrors({ ...error })
    return !!error
  }

  const handleOnSubmit = () => {
    if (validate()) return
    setDialog(state => ({ ...state, alert: true }))
  }

  const handleOnCancel = () => {
    setReassignErrors({
      note: '',
      assignee: ''
    })
    setDialog(state => ({ ...state, assign: false }))
  }

  return (
    <div>
      <React.Fragment>
        <Dialog
          open={dialog.assign}
          onClose={() => setDialog(state => ({ ...state, assign: false }))}
          PaperProps={{ style: { height: 'auto', display: 'flex' } }}
          className={classes['reassign']}
          fullWidth
          maxWidth='md'
          aria-labelledby='customized-dialog-title'
        >
          <DialogTitle disableTypography className={classes['header-container']}>
            <div className={classes.header}>
              <Typography variant='h6' className={classes.title}>
                Notes
              </Typography>
              <IconButton
                aria-label='close'
                className={classes.button}
                onClick={() => setDialog(state => ({ ...state, assign: false }))}
              >
                <CloseIcon />
              </IconButton>
            </div>
          </DialogTitle>

          <DialogContent dividers className={classes['content']}>
            <div className={classes.input}>
              <TextField
                className={classes.comment}
                multiline
                rows={5}
                id=''
                value={reassign.note}
                autoFocus
                fullWidth
                placeholder='Compose a Note'
                onChange={event => dispatch(setReassignNotes(event.target.value))}
                // onChange={event => dispatch(setEscalationMessage(event.target.value))}
                error={reassignErrors.note}
              />
            </div>
            <div className={classes.footer}>
              <div className={classes.assign}>
                <Typography variant='subtitle2' className={classes.to}>
                  {dropDownUsers && dropDownUsers.length > 0 ? 'Assign To' : 'No Users'}
                </Typography>
                <DropDown
                  id='escalate-to'
                  items={dropDownUsers}
                  value={reassign.assignee.id}
                  // onChange={event =>
                  //   dispatch(
                  //     setEscalationAssignee({
                  //       id: event.target.value,
                  //       name: event.target.value
                  //     })
                  //   )
                  // }
                  onChange={event => {
                    dispatch(
                      setReassignAssignee({
                        id: event.target.value,
                        name: event.target.value
                      })
                    )
                  }}
                  error={reassignErrors.assignee}
                  disableComponents={!!(dropDownUsers && dropDownUsers.length === 0)}
                />
              </div>

              <div className={classes.action}>
                <Button
                  onClick={() => handleOnCancel()}
                  variant='contained'
                  color='primary'
                  className='button-negative'
                >
                  Cancel
                </Button>
                <Button
                  onClick={() => handleOnSubmit()}
                  variant='contained'
                  color='primary'
                  className='button-positive'
                >
                  Submit
                </Button>
              </div>
            </div>
          </DialogContent>
          {/*
          <DialogActions>
            <Button
              onClick={() => setDialog(false)}
              variant='contained'
              color='primary'
              id='save-for-now'
              className='button-negative'
            >
              Cancel
            </Button>
            <Button
              onClick={() => {}}
              variant='contained'
              color='primary'
              id='save-for-now'
              className='button-positive'
            >
              Submit
            </Button>
          </DialogActions> */}
        </Dialog>
        <Dialog
          open={dialog.alert}
          onClose={() => setDialog(state => ({ ...state, alert: false }))}
          aria-labelledby='alert-dialog-title'
          aria-describedby='alert-dialog-description'
          maxWidth='xs'
        >
          <DialogTitle className={classes['alert-title']}>
            <Typography variant='inherit'>Assign SOA to Claim Encoder</Typography>
          </DialogTitle>

          <DialogContent className={classes['message-content']}>
            <div className={classes['message']}>
              <Typography className={classes['message-child']} variant='body1'>
                Are you sure you want to submit the SOA to the selected Claim Encoder?
              </Typography>
              <Typography variant='body1'>
                This SOA will be sent to the assigned Claim Encoder for processing.
              </Typography>
            </div>
          </DialogContent>

          <DialogActions className={classes['footer-action']}>
            <React.Fragment>
              <Button
                onClick={() => setDialog(state => ({ ...state, alert: false }))}
                variant='contained'
                color='primary'
                className='button-negative'
              >
                Cancel
              </Button>
              <Button
                onClick={() => {
                  handleOnSubmitReassign()
                  setDialog(state => ({ ...state, alert: false, assign: false }))
                }}
                variant='contained'
                color='primary'
                className='button-positive'
              >
                Submit
              </Button>
            </React.Fragment>
          </DialogActions>
        </Dialog>
      </React.Fragment>
    </div>
  )
}
