import React from 'react'
import Grid from '@material-ui/core/Grid'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import TaskTray from 'SharedComponents/TaskTrayComponent/TaskTrayComponent'
import TicketList from 'SharedComponents/TicketListComponent/TicketListComponent'
import ClaimsEncoderTile from '../../Components/ClaimsEncoderTile/ClaimsEncoderTile'
import OpenBatchesTile from '../../Components/OpenBatchesTile/OpenBatchesTile'
import MyBatchesTile from '../../Components/MyBatchesTile/MyBatchesTile'
import SubmittedBatchesTile from '../../Components/SubmittedBatchesTile/SubmittedBatchesTile'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import ClaimsEncoderSupervisorServices from 'Services/Api/ClaimsEncoderSupervisorServices/ClaimsEncoderSupervisorServices'
// import ClaimsEncodingServices from 'Services/Api/ClaimsEncodingServices'

// Styles
import classes from './Dashboard.module.scss'

export default function Dashboard (props) {
  React.useEffect(() => {
    PageTitleService('Dashboard')
    BreadcrumbsService.setSelectedItems(['dashboard'])
  }, [])

  return (
    <div className={classes.root}>
      <Grid container className={`${classes.tiles} ${classes.row}`} spacing={3} direction='column'>
        <Grid item className={classes['task-tray']}>
          <TaskTray sizes={{ xs: 8 }} title='Claims Encoding'>
            <OpenBatchesTile services={ClaimsEncoderSupervisorServices} sizes={{ sm: 12 }} {...props} />
            <MyBatchesTile services={ClaimsEncoderSupervisorServices} sizes={{ sm: 12 }} {...props} />
            <SubmittedBatchesTile services={ClaimsEncoderSupervisorServices} sizes={{ sm: 12 }} {...props} />
          </TaskTray>

          <Grid item className={classes['task-tray-monitor']}>
            <TaskTray sizes={{ xs: 8 }} title='Items to Monitor'>
              <ClaimsEncoderTile services={ClaimsEncoderSupervisorServices} sizes={{ sm: 12 }} {...props} />
            </TaskTray>
          </Grid>
        </Grid>

        <Grid item className={classes['ticket-list']}>
          <TicketList count={0} title='Open Tickets' viewAllLink={{ to: '/', caption: 'View all' }}>
            <Table size='small'>
              <TableHead>
                <TableRow>
                  <TableCell id='cvs-ticket-no'>Ticket #</TableCell>
                  <TableCell id='cvs-date'>Date</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {[1, 2].map((item, index) => (
                  <TableRow key={index}>
                    <TableCell id={`cvs-ticket-no-${index}`}>2334324343</TableCell>
                    <TableCell id={`cvs-date-${index}`}>08/12/2019</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TicketList>
        </Grid>
      </Grid>
    </div>
  )
}
